import { FC } from 'react';

import { Builder, Element, useFilesContext, useNode } from '@king-david-it/builder';
import { Theme, useMediaQuery } from '@mui/material';
import { DEFAULT_LOCALE } from 'application/constants';
import { TeamMember, TeamSection, TeamSectionProps } from 'components/sections/TeamSection';
import { useLocaleContext } from 'contexts/LocaleContext';
import { useTranslate } from 'hooks/index';
import { get, merge } from 'lodash';
import { GridItem } from 'templates/GridItem';
import { SectionSwiper } from 'views/SectionSwiper';
import { VerticalAvatarView } from 'views/VerticalAvatarView';

import { getChoicesFromArray } from '../helpers/getChoicesFromArray';
import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

interface TeamSectionElementProps extends Omit<TeamSectionProps, 'title' | 'subtitle'> {
  membersCount: string;
  title?: Record<string, string>;
  subtitle?: Record<string, string>;
}

const DEFAULT_AVATAR_SIZE = 220;

const TeamTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ wordBreak: 'break-word', ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(TeamTextElement, {
  ...textElementSettings,
  name: 'TeamTextElement',
});

export const TeamMemberElement: FC<TeamMember> = (props) => {
  const { avatarSrc: avatarSrcProp, position, name, mediaLinks } = props;
  const { locale } = useLocaleContext();
  const {
    connectors: { connect },
  } = useNode();

  const { getFile } = useFilesContext();
  const avatarSrc = getFile(avatarSrcProp)?.src;
  const title = get(name, locale) || get(name, DEFAULT_LOCALE);
  const subtitle = get(position, locale) || get(position, DEFAULT_LOCALE);

  return (
    <GridItem
      ref={(el) => {
        el?.addEventListener('click', () => {
          // Create a new mousedown event
          const mouseDownEvent = new MouseEvent('mousedown', {
            view: window,
          });

          // Dispatch the mousedown event on the element
          el.dispatchEvent(mouseDownEvent);
        });

        if (el) {
          connect(el);
        }
      }}
      container
      justifyContent="center"
    >
      <VerticalAvatarView
        size={DEFAULT_AVATAR_SIZE}
        title={title}
        subtitle={subtitle}
        avatarSrc={avatarSrc}
        {...mediaLinks}
      />
    </GridItem>
  );
};

Builder.registerComponent<ComponentSpec>(TeamMemberElement, {
  name: 'TeamMemberElement',
  displayName: 'Team member',
  inputs: [
    {
      name: 'name',
      displayName: 'Name',
      type: 'string',
      defaultValue: 'Name',
      isTranslatable: true,
    },
    {
      name: 'position',
      displayName: 'Position',
      type: 'string',
      defaultValue: 'CEO',
      isTranslatable: true,
    },
    {
      name: 'avatarSrc',
      displayName: 'Avatar image',
      type: 'image',
      minWidth: 220,
      minHeight: 220,
    },
    {
      name: 'mediaLinks.facebookLink',
      displayName: 'Facebook link',
      type: 'string',
    },
    {
      name: 'mediaLinks.twitterLink',
      displayName: 'Twitter link',
      type: 'string',
    },
    {
      name: 'mediaLinks.instagramLink',
      displayName: 'Instagram link',
      type: 'string',
    },
  ],
});

export const TeamSectionElement: FC<TeamSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp, membersCount, maxWidth } = props;
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));
  const {
    connectors: { connect },
  } = useNode();

  const t = useTranslate({ basePath: 'builder.elements.default_values' });

  return (
    <TeamSection
      ref={(el) => el && connect(el)}
      {...props}
      title={
        <Element
          id="team_section_element_title"
          // @ts-expect-error why types are not compatible?
          is={TeamTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_title'),
              'fr-FR': t('text_element_title'),
              'he-IL': t('text_element_title'),
            },
            titleProp,
          )}
          align="center"
          variant="h3"
          component="h3"
        />
      }
      subtitle={
        <Element
          id="team_section_element_subtitle"
          // @ts-expect-error why types are not compatible?
          is={TeamTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_subtitle'),
              'fr-FR': t('text_element_subtitle'),
              'he-IL': t('text_element_subtitle'),
            },
            subtitleProp,
          )}
          align="center"
          variant="subtitle1"
        />
      }
      maxWidth={maxWidth}
    >
      <SectionSwiper slidesPerView={isMobile ? 1 : 3} centerInsufficientSlides={true}>
        {Array(Number(membersCount))
          .fill(null)
          .map((_, index) => {
            // @ts-expect-error why types are not compatible?
            return <Element key={index} id={`team_${index + 1}`} is={TeamMemberElement} />;
          })}
      </SectionSwiper>
    </TeamSection>
  );
};

const membersChoices = getChoicesFromArray(
  Array(12)
    .fill(null)
    .map((_, i) => String(i + 1)),
);

Builder.registerComponent<ComponentSpec>(TeamSectionElement, {
  name: 'TeamSectionElement',
  displayName: 'Team section',
  inputs: [
    {
      name: 'membersCount',
      displayName: 'Team members count',
      type: 'select',
      choices: membersChoices,
      defaultValue: '3',
    },
    {
      name: 'title',
      displayName: 'Title',
      type: 'string',
      defaultValue: 'Enter some title here...',
      isTranslatable: true,
    },
    {
      name: 'subtitle',
      displayName: 'Subtitle',
      type: 'string',
      defaultValue: 'Enter some subtitle here...',
      isTranslatable: true,
    },
    {
      name: 'maxWidth',
      displayName: 'Max width',
      type: 'select',
      choices: getChoicesFromArray(['sm', 'md', 'lg', 'xl']),
      defaultValue: 'lg',
    },
  ],
});
