import { FC } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { DEFAULT_LOCALE } from 'application/constants';
import { Typography, TypographyProps } from 'components/Typography';
import { useLocaleContext } from 'contexts/LocaleContext';
import get from 'lodash/get';

import { getChoicesFromArray } from './helpers/getChoicesFromArray';
import { ComponentSpec } from './types';

export interface TextElementProps extends TypographyProps {
  text: Record<string, string>;
}

export const TextElement: FC<TextElementProps> = (props) => {
  const { text: textProp, color, variant, align, sx, component } = props;

  const {
    connectors: { connect },
  } = useNode();

  const { locale } = useLocaleContext();

  const text = get(textProp, locale) || get(textProp, DEFAULT_LOCALE);

  return (
    <Typography
      ref={(el: HTMLElement) => {
        el?.addEventListener('click', () => {
          // Create a new mousedown event
          const mouseDownEvent = new MouseEvent('mousedown', {
            view: window,
          });

          // Dispatch the mousedown event on the element
          el.dispatchEvent(mouseDownEvent);
        });

        if (el) {
          connect(el);
        }
      }}
      color={color}
      variant={variant}
      align={align}
      sx={sx}
      {...(component && { component })}
    >
      {text}
    </Typography>
  );
};

export const textElementSettings: ComponentSpec = {
  name: 'TextElement',
  displayName: 'Text',
  icon: '/icons/text.svg',
  inputs: [
    {
      name: 'text',
      displayName: 'Text',
      type: 'string',
      defaultValue: 'Enter some text...',
      isTranslatable: true,
    },
    {
      name: 'color',
      displayName: 'Color',
      type: 'select',
      choices: [
        { id: 'primary.main', name: 'Primary' },
        { id: 'secondary.main', name: 'Secondary' },
        { id: 'primary.contrastText', name: 'White' },
        { id: 'text.primary', name: 'Text primary' },
        { id: 'text.secondary', name: 'Text secondary' },
        { id: 'success.main', name: 'Success' },
        { id: 'error.main', name: 'Error' },
        { id: 'info.main', name: 'Info' },
        { id: 'warning.main', name: 'Warning' },
      ],
      defaultValue: 'text.primary',
    },
    {
      name: 'variant',
      displayName: 'Variant',
      type: 'select',
      choices: getChoicesFromArray([
        'h1',
        'h2',
        'h3',
        'h4',
        'h5',
        'h6',
        'body1',
        'body2',
        'subtitle1',
        'subtitle2',
        'button',
        'buttonUnderline',
        'small1',
        'small2',
      ]),
      defaultValue: 'body1',
    },
    {
      name: 'component',
      displayName: 'Component (Impact on SEO and Accessibility)',
      type: 'select',
      choices: getChoicesFromArray(['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'p', 'span', 'div']),
    },
    {
      name: 'align',
      displayName: 'Align',
      type: 'select',
      choices: getChoicesFromArray(['left', 'right', 'center', 'justify']),
    },
  ],
};

Builder.registerComponent<ComponentSpec>(TextElement, textElementSettings);
