import { forwardRef } from 'react';

import { Box } from '@mui/material';
import { Identifier } from '@packages/types';

import { ListingCard } from './ListingCard';
import { SingleListingSwiper } from './swiper';

interface SingleListingsCarouselSectionProps {
  ids?: Identifier[];
  buttonText: string;
}

export const SingleListingsCarouselSection = forwardRef<
  HTMLDivElement,
  SingleListingsCarouselSectionProps
>((props, ref) => {
  // added empty string id to have mock view of listings
  const { ids = [''], buttonText } = props;

  return (
    <Box
      ref={ref}
      sx={{
        '.listing_swiper': {
          '.swiper-pagination-bullet': {
            width: 10,
            height: 10,
            bgcolor: 'transparent',
            border: '1px solid',
            borderColor: 'primary.contrastText',
            borderRadius: 1,
          },
          '.swiper-pagination-bullet-active': {
            bgcolor: 'primary.contrastText',
          },
        },
      }}
    >
      <SingleListingSwiper className="listing_swiper" pagination={true} navigation={true}>
        {ids?.map((id) => (
          <ListingCard key={id} id={id} buttonText={buttonText} />
        ))}
      </SingleListingSwiper>
    </Box>
  );
});

SingleListingsCarouselSection.displayName = 'SingleListingsCarouselSection';
