import { fetchClient } from '@packages/providers';
import { ExecuteRecaptcha } from '@packages/types';
import { CONTACT_US_RESOURCE } from 'application/resources';

import { ContactFormData } from './schema';

type ContactParams = {
  data: ContactFormData;
  executeRecaptcha: ExecuteRecaptcha;
};

export const contact = (params: ContactParams) => {
  const { data, executeRecaptcha } = params;
  return fetchClient.post({ pathname: CONTACT_US_RESOURCE, data, executeRecaptcha });
};
