import { forwardRef, ReactNode } from 'react';

import { alpha, Box, Container, Divider, Grid } from '@mui/material';
import { Link, LinkProps } from 'components/links/Link';
import { Typography } from 'components/Typography';
import { IcoMoon } from 'custom/IcoMoon';
import WithTitleTemplate, { WithTitleTemplateProps } from 'templates/WithTitleTemplate';
import { theme } from 'theme/theme';
import { SocialsToolbar, SocialsToolbarProps } from 'views/SocialsToolbar';

export interface ContactUsSectionProps {
  phone?: string;
  email?: string;
  address?: string;
  mediaLinks?: SocialsToolbarProps;
  text?: ReactNode;
}

export const ContactUsSection = forwardRef<HTMLDivElement, ContactUsSectionProps>((props, ref) => {
  const { phone, email, address, mediaLinks, text } = props;

  const defaultWithTitleTemplateProps: Partial<WithTitleTemplateProps> = {
    sx: { display: 'flex', flexDirection: 'column', gap: 2 },
    space: 0,
  };
  const defaultLinkProps: Partial<LinkProps> = {
    sx: {
      display: 'block',
      maxWidth: 220,
      mx: 'auto',
      textDecoration: 'none',
      color: theme.palette.text.primary,
    },
  };

  return (
    <Box
      ref={ref}
      sx={{
        px: { xs: 2, md: 0 },
        pt: { xs: 11.5, md: 6.5 },
        pb: 5.5,
        background: alpha(theme.palette.primary.background, 0.5),
      }}
    >
      <Container maxWidth="lg">
        <Grid
          item
          container
          flexWrap="nowrap"
          flexDirection={{ xs: 'column', md: 'row' }}
          justifyContent="space-between"
          columnSpacing={3}
          rowSpacing={7}
        >
          {address ? (
            <Grid item xs>
              <Link
                {...defaultLinkProps}
                href={`http://maps.google.com/?q=${address}`}
                target="_blank"
              >
                <WithTitleTemplate
                  {...defaultWithTitleTemplateProps}
                  title={<Typography variant="subtitle1">{address}</Typography>}
                >
                  <IcoMoon icon="pin" />
                </WithTitleTemplate>
              </Link>
            </Grid>
          ) : null}

          {email ? (
            <Grid item xs>
              <Link {...defaultLinkProps} href={`mailto:${email}`}>
                <WithTitleTemplate
                  {...defaultWithTitleTemplateProps}
                  title={<Typography variant="subtitle1">{email}</Typography>}
                >
                  <IcoMoon icon="email" />
                </WithTitleTemplate>
              </Link>
            </Grid>
          ) : null}
          {phone ? (
            <Grid item xs>
              <Link {...defaultLinkProps} href={`tel:${phone}`}>
                <WithTitleTemplate
                  {...defaultWithTitleTemplateProps}
                  title={<Typography variant="subtitle1">{phone}</Typography>}
                >
                  <IcoMoon icon="phone" />
                </WithTitleTemplate>
              </Link>
            </Grid>
          ) : null}
        </Grid>

        <Divider sx={{ my: 5.25 }} />

        {text && (
          <Grid container justifyContent="center" mb={3.5}>
            <Box textAlign="center" maxWidth={464}>
              {text}
            </Box>
          </Grid>
        )}

        <Grid container justifyContent="center">
          <SocialsToolbar {...mediaLinks} />
        </Grid>
      </Container>
    </Box>
  );
});

ContactUsSection.displayName = 'ContactUsSection';
