import { fetchClient, ReviewTypes, zodReviewSchemas } from '@packages/providers';
import { useQuery } from '@tanstack/react-query';
import { REVIEWS_RESOURCE } from 'application/resources';

const getQueryFn = (params: { perPage?: number }) => async (): Promise<ReviewTypes['index']> => {
  const { perPage } = params;
  const {
    data: { data },
  } = await fetchClient.get({
    pathname: REVIEWS_RESOURCE,
    perPage,
    filter: {
      is_promoted: 1,
    },
    sort: {
      field: 'added_at',
      order: 'DESC',
    },
  });

  return zodReviewSchemas.index.parse(data);
};

export const useGetRewiews = (params?: { perPage?: number }) => {
  const { perPage } = params || {};
  return useQuery({
    queryKey: [REVIEWS_RESOURCE, params],
    queryFn: getQueryFn({ perPage }),
    enabled: true,
    refetchOnMount: false,
  });
};

export default useGetRewiews;
