import { forwardRef, ReactNode } from 'react';

import { Box } from '@mui/material';
import { NextImage } from '@packages/components';
import { StyledRootProps, WithRootProps } from '@packages/types';
import { DarkenedImageBackground } from 'custom/DarkenedImageBackground';

export type PageHeadingSectionProps = WithRootProps<
  StyledRootProps,
  {
    title: ReactNode;
    subtitle: ReactNode;
    mobileImageSrc?: string;
    desktopImageSrc?: string;
  }
>;

export const PageHeadingSection = forwardRef<HTMLElement, PageHeadingSectionProps>((props, ref) => {
  const { sx, className, desktopImageSrc, mobileImageSrc, title, subtitle } = props;

  return (
    <Box
      ref={ref}
      className={className}
      sx={{
        height: 496,
        width: '100%',
        position: 'relative',
        ...sx,
      }}
    >
      <DarkenedImageBackground />
      {mobileImageSrc ? (
        <Box sx={{ display: { md: 'none' } }}>
          <NextImage
            src={mobileImageSrc}
            alt="Page heading"
            fill
            style={{ objectFit: 'cover' }}
            priority
          />
        </Box>
      ) : null}
      {desktopImageSrc ? (
        <Box sx={{ display: { xs: 'none', md: 'block' } }}>
          <NextImage
            src={desktopImageSrc}
            alt="Page heading"
            fill
            style={{ objectFit: 'cover' }}
            priority
          />
        </Box>
      ) : null}
      <Box
        sx={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: { sm: '100%', md: 'auto' },
          zIndex: 2,
        }}
      >
        {/* Render <Element /> for `title` and `subtitle` */}
        {title}
        {subtitle}
      </Box>
    </Box>
  );
});

PageHeadingSection.displayName = 'PageHeadingSection';
