import { FC } from 'react';

import { Box, Chip, Divider, Grid, Theme, useMediaQuery } from '@mui/material';
import { NextImage } from '@packages/components';
import { Identifier } from '@packages/types';
import {
  ACCOMMODATES_SOURCE,
  BASE_PRICE_SLUG,
  BATHROOMS_SOURCE,
  BEDROOMS_SOURCE,
  listingRoute,
  RATING_COUNT_SLUG,
  RATING_SLUG,
} from 'application/constants';
import { Link } from 'components/links/Link';
import { getFilterValues } from 'helpers/getFilterValues';
import useGetPriceInCurrentCurrency from 'helpers/useGetPriceInCurrentCurrency';
import { useGetListing, useTranslate } from 'hooks/index';
import { get } from 'lodash';
import { GridItem } from 'templates/GridItem';
import { HEADER_HEIGHT, MOBILE_HEADER_HEIGHT } from 'theme';
import { ListingInfoBlock } from 'views/ListingInfoBlock';

interface ListingCardProps {
  id: Identifier;
  buttonText: string;
}

export const ListingCard: FC<ListingCardProps> = (props) => {
  const { id, buttonText } = props;
  const { data } = useGetListing(id);

  const getPrice = useGetPriceInCurrentCurrency();
  const translate = useTranslate();
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  // use first image by default
  const src = get(data, 'gallery.0.src');
  const title = get(data, 'title') || '';
  const address = get(data, 'address') || '';
  const formattedFilters = get(data, 'formatted_filters');
  const listingSlug = get(data, 'slug');

  const [accommodates, bedrooms, bathrooms, basePrice, rating, ratingCount] = getFilterValues({
    formattedFilters: data?.formatted_filters || [],
    slugs: [
      ACCOMMODATES_SOURCE,
      BEDROOMS_SOURCE,
      BATHROOMS_SOURCE,
      BASE_PRICE_SLUG,
      RATING_SLUG,
      RATING_COUNT_SLUG,
    ],
  });

  const priceValue = basePrice?.value ? Number(basePrice?.value) : 0; // TODO: convert
  const convertedPrice = getPrice({
    amount: priceValue,
    initialCurrencyCode: data?.currency?.code || '',
  });

  const amenitiesItems = [];
  if (accommodates) {
    amenitiesItems.push({
      id: '1',
      label: translate.root('custom.guest'),
      value: accommodates.value,
    });
  }
  if (bedrooms) {
    amenitiesItems.push({
      id: '2',
      label: bedrooms.filterAttribute?.title,
      value: bedrooms.value,
    });
  }
  if (bathrooms) {
    amenitiesItems.push({
      id: '3',
      label: bathrooms.filterAttribute?.title,
      value: bathrooms.value,
    });
  }

  return (
    <Box
      height={742}
      width={1}
      position="relative"
      sx={{
        display: 'block',
        maxHeight: {
          xs: `calc(100vh - ${MOBILE_HEADER_HEIGHT}px)`,
          md: `calc(100vh - ${HEADER_HEIGHT}px)`,
        },
        ...(!src
          ? {
              background: `linear-gradient(0deg, rgba(48, 48, 48, 0.68), rgba(48, 48, 48, 0.68))`,
            }
          : {}),
      }}
    >
      {src && <NextImage src={src} fill style={{ objectFit: 'cover' }} alt={address} />}
      <Grid container height={1} flexDirection="column" justifyContent="space-between">
        <Grid item container justifyContent="center" mt={8}>
          <Grid item xs={6} md={2} zIndex={1}>
            <Chip
              variant="filled"
              color="primary"
              label={buttonText}
              sx={{
                borderRadius: 6,
                width: 1,
                ':hover': {
                  bgcolor: 'primary.main',
                },
              }}
            />
          </Grid>
        </Grid>
        <Grid
          item
          container
          px={4}
          pt={{ xs: 16, md: 10 }}
          pb={4}
          zIndex={1}
          sx={{
            background: 'linear-gradient(180deg, rgba(38, 50, 56, 0) 0%, #263238 100%)',
          }}
          justifyContent="center"
        >
          <Link
            href={listingSlug ? listingRoute.getPathname(listingSlug) : ''}
            target={isMobile ? '_self' : '_blank'}
            underline="none"
            maxWidth="100%"
            aria-label={title}
          >
            <GridItem maxWidth={{ md: 600 }} sx={{ color: 'primary.contrastText' }}>
              {formattedFilters && (
                <ListingInfoBlock
                  name={title}
                  address={address}
                  price={convertedPrice}
                  pricePrefix="from"
                  divider={
                    <Divider
                      sx={{ borderColor: 'primary.contrastText' }}
                      orientation="vertical"
                      flexItem
                    />
                  }
                  amenitiesItems={amenitiesItems}
                  amenitiesTypographyProps={{ sx: { color: 'primary.contrastText' } }}
                  ratingTypographyProps={{ sx: { color: 'primary.contrastText' } }}
                  rating={Number(rating?.value) || 0}
                  reviewsCount={Number(ratingCount?.value) || 0}
                />
              )}
            </GridItem>
          </Link>
        </Grid>
      </Grid>
    </Box>
  );
};
