import { FC } from 'react';

import { Box, BoxProps, useTheme } from '@mui/material';
import { NextImage } from '@packages/components';
import { IcoMoon } from 'custom/IcoMoon';
import { ImageProps } from 'next/image';

interface AvatarProps extends BoxProps {
  src?: ImageProps['src'];
  size: number;
}

export const Avatar: FC<AvatarProps> = (props) => {
  const { src, size, sx, ...restAvatarProps } = props;

  const theme = useTheme();

  return (
    <Box
      sx={{
        position: 'relative',
        borderRadius: '50%',
        overflow: 'hidden',
        width: size,
        height: size,
        ...sx,
      }}
      {...restAvatarProps}
    >
      {src ? (
        <NextImage src={src} fill style={{ objectFit: 'cover' }} alt="Avatar image" />
      ) : (
        <IcoMoon
          icon="account-circle"
          color={theme.palette.text.disabled}
          sx={{ fontSize: size }}
        />
      )}
    </Box>
  );
};
