import { FC, ReactNode, useMemo } from 'react';

import {
  createTheme,
  CssBaseline,
  darken,
  lighten,
  ThemeProvider as MuiThemeProvider,
  Theme,
} from '@mui/material';
import * as locales from '@mui/material/locale';
import { deepmerge } from '@mui/utils';
import { useLocaleContext } from 'contexts/LocaleContext';
import { getDirectionFromLocale } from 'helpers/getDirectionFromLocale';
import { useGetWebsite } from 'hooks/index';
import { get } from 'lodash';
import Head from 'next/head';
import { DEFAULT_BUTTON_RADIUS, theme as defaultTheme } from 'theme/index';

type SupportedLocales = keyof typeof locales;

export const ThemeProvider: FC<{ children: ReactNode }> = ({ children }) => {
  const { locale } = useLocaleContext();
  const { data: websiteData } = useGetWebsite();

  const customFontFamily = get(websiteData, 'styles.font_family');

  const theme: Theme = useMemo(() => {
    const primaryColor = get(websiteData, 'color_primary') || defaultTheme.palette.primary.main;
    const secondaryColor =
      get(websiteData, 'color_secondary') || defaultTheme.palette.secondary.main;
    const buttonBorderRadius =
      get(websiteData, 'styles.button_border_radius') || DEFAULT_BUTTON_RADIUS;

    const primaryLight = lighten(primaryColor, 0.88);
    const secondaryLight = lighten(secondaryColor, 0.88);
    const primaryDark = darken(primaryColor, 0.19);
    const secondaryDark = darken(secondaryColor, 0.19);

    // en-US -> enUS. https://mui.com/material-ui/guides/localization/#supported-locales
    const localeImportName = locale.replace('-', '');

    const theme = createTheme(
      deepmerge(defaultTheme, {
        direction: getDirectionFromLocale(locale),
        palette: {
          primary: {
            main: primaryColor,
            light: primaryLight,
            dark: primaryDark,
          },
          secondary: {
            main: secondaryColor,
            light: secondaryLight,
            dark: secondaryDark,
          },
        },
        websiteSettings: {
          buttonBorderRadius,
        },
      }),
      locales[localeImportName as SupportedLocales],
    );

    if (customFontFamily) {
      const defaultFontFamily = theme.typography.fontFamily;
      const fontFamily = `${customFontFamily}, ${defaultFontFamily}`;

      /**
       * @see https://github.com/mui/material-ui/issues/35939#issuecomment-1406913737
       * Due to a MUI bug, we can't extend the theme with the new `fontFamily`,
       * so we use the next approach.
       * Note: `JSON.stringify()` removes methods, e.g. `pxToRem()`, etc., but
       * `createTheme()` below will add them again
       */
      const stringifiedThemeTypography = JSON.stringify(theme.typography);
      const stringifiedThemeTypographyWithCustomFontFamily = stringifiedThemeTypography.replaceAll(
        `"${defaultFontFamily}"`,
        `"${fontFamily}"`,
      );

      return createTheme({
        ...theme,
        typography: JSON.parse(stringifiedThemeTypographyWithCustomFontFamily),
      });
    }

    return theme;
  }, [locale, websiteData, customFontFamily]);

  return (
    <MuiThemeProvider theme={theme}>
      {customFontFamily ? (
        <Head>
          <CustomFontLink fontFamily={customFontFamily} />
        </Head>
      ) : null}
      <CssBaseline />
      {children}
    </MuiThemeProvider>
  );
};

const CustomFontLink: FC<{ fontFamily: string }> = (props) => {
  const { fontFamily } = props;

  const formattedFontFamily = fontFamily.split(' ').join('+');

  return (
    <>
      <link
        rel="stylesheet"
        href={`https://fonts.googleapis.com/css2?family=${formattedFontFamily}:wght@300;400;500;600;700&display=swap`}
      />
    </>
  );
};
