import { FC, ReactNode, useMemo } from 'react';

import { Grow, Slide, useMediaQuery } from '@mui/material';
import { Notification } from 'custom/Notification';
import {
  SnackbarProvider as NotistackSnackbarProvider,
  SnackbarProviderProps as NotistackSnackbarProviderProps,
} from 'notistack';
import { ITheme } from 'theme/index';

interface SnackbarProviderProps {
  children: ReactNode;
}

export const SnackbarProvider: FC<SnackbarProviderProps> = (props) => {
  const { children } = props;

  const isMobile = useMediaQuery<ITheme>((theme) => theme.breakpoints.down('md'));

  const snackbarProviderProps: NotistackSnackbarProviderProps = useMemo(
    () => ({
      maxSnack: 2,
      anchorOrigin: {
        vertical: 'bottom',
        horizontal: 'center',
      },
      Components: {
        default: Notification,
        error: Notification,
        info: Notification,
        success: Notification,
        warning: Notification,
      },
      TransitionComponent: isMobile ? Slide : Grow,
    }),
    [isMobile],
  );

  return (
    <NotistackSnackbarProvider {...snackbarProviderProps}>{children}</NotistackSnackbarProvider>
  );
};
