import { FC } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { Box, Theme, useMediaQuery } from '@mui/material';
import { DEFAULT_LOCALE } from 'application/constants';
import { useLocaleContext } from 'contexts/LocaleContext';
import get from 'lodash/get';

import { getChoicesFromArray } from './helpers/getChoicesFromArray';
import { ComponentSpec } from './types';

import { Button, ButtonProps } from '../../components/buttons/Button';

export interface ButtonElementProps extends ButtonProps {
  text?: Record<string, string>;
  borderRadius?: number;
  align?: 'flex-start' | 'flex-end' | 'center';
}

export const ButtonElement: FC<ButtonElementProps> = (props) => {
  const {
    text: textProp,
    sx,
    borderRadius,
    fullWidth: fullWidthProp,
    align,
    color: colorProp,
    ...rest
  } = props;

  const {
    connectors: { connect },
  } = useNode();

  const { locale } = useLocaleContext();
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const text = get(textProp, locale) || get(textProp, DEFAULT_LOCALE);

  const fullWidth = fullWidthProp || isMobile;

  return (
    <Box display="flex" justifyContent={align} sx={{ ...(fullWidth && { width: '100%' }) }}>
      <Button
        sx={{
          ...sx,
          ...(borderRadius && { borderRadius: `${borderRadius}px` }),
        }}
        ref={(el: HTMLButtonElement) => {
          el?.addEventListener('click', (event) => {
            // TODO: RM-8618
            event.stopPropagation();
            // Create a new mousedown event
            const mouseDownEvent = new MouseEvent('mousedown', {
              view: window,
            });

            // Dispatch the mousedown event on the element
            el.dispatchEvent(mouseDownEvent);
          });

          if (el) {
            connect(el);
          }
        }}
        fullWidth={fullWidth}
        color={colorProp || 'primary'} // Add primary when selected empty value ''
        {...rest}
      >
        {text}
      </Button>
    </Box>
  );
};

Builder.registerComponent<ComponentSpec>(ButtonElement, {
  name: 'ButtonElement',
  displayName: 'Button',
  icon: '/icons/button.svg',
  inputs: [
    {
      name: 'text',
      displayName: 'Text',
      type: 'string',
      defaultValue: 'Enter text...',
      isTranslatable: true,
    },
    {
      name: 'color',
      displayName: 'Color',
      type: 'select',
      choices: getChoicesFromArray([
        'inherit',
        'primary',
        'secondary',
        'success',
        'error',
        'info',
        'warning',
      ]),
      defaultValue: 'primary',
    },
    {
      name: 'variant',
      displayName: 'Variant',
      type: 'select',
      choices: getChoicesFromArray(['contained', 'outlined', 'text']),
      defaultValue: 'contained',
    },
    {
      name: 'size',
      displayName: 'Size',
      type: 'select',
      choices: getChoicesFromArray(['small', 'medium', 'large']),
      defaultValue: 'medium',
    },
    {
      name: 'fullWidth',
      displayName: 'Full width',
      type: 'boolean',
      defaultValue: false,
    },
    {
      name: 'align',
      displayName: 'Alignment',
      type: 'select',
      choices: getChoicesFromArray(['flex-start', 'center', 'flex-end']),
      defaultValue: 'flex-start',
    },
    {
      name: 'borderRadius',
      displayName: 'Border radius (px)',
      type: 'number',
      min: 0,
      max: 20,
      helperText: 'Each point is 1px',
    },
  ],
});
