import { FC } from 'react';

import { Box } from '@mui/material';
import { StyledRootProps } from '@packages/types';
export type DarkenedImageBackgroundProps = StyledRootProps;

/* We need this Box for blackout image */
export const DarkenedImageBackground: FC<DarkenedImageBackgroundProps> = (props) => {
  const { sx, className } = props;
  return (
    <Box
      className={className}
      sx={{
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
        background: 'rgba(48, 48, 48, 0.72)',
        zIndex: 1,
        ...sx,
      }}
    />
  );
};
