import { Box, IconButton, Link } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { IcoMoon } from 'custom/IcoMoon';
import { getUrlWithHttp } from 'helpers/getUrlWithHttp';
import { useTranslate } from 'hooks/index';

export interface SocialsToolbarProps extends StyledRootProps {
  facebookLink?: string;
  instagramLink?: string;
  twitterLink?: string;
  buttonSize?: 'small' | 'medium' | 'large';
}

export function SocialsToolbar(props: SocialsToolbarProps) {
  const { facebookLink, instagramLink, twitterLink, sx, className, buttonSize = 'medium' } = props;
  const translate = useTranslate({ basePath: 'social_media' });

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        width: 180,
        gap: 3,
        ...sx,
      }}
      className={className}
    >
      {facebookLink ? (
        <Link
          target="_blank"
          href={getUrlWithHttp(facebookLink)}
          aria-label={translate('facebook')}
        >
          <IconButton size={buttonSize}>
            <IcoMoon icon="facebook" />
          </IconButton>
        </Link>
      ) : null}
      {instagramLink ? (
        <Link
          target="_blank"
          href={getUrlWithHttp(instagramLink)}
          aria-label={translate('instagram')}
        >
          <IconButton size={buttonSize}>
            <IcoMoon icon="instagram" />
          </IconButton>
        </Link>
      ) : null}
      {twitterLink ? (
        <Link target="_blank" href={getUrlWithHttp(twitterLink)} aria-label={translate('twitter')}>
          <IconButton size={buttonSize}>
            <IcoMoon icon="twitter" />
          </IconButton>
        </Link>
      ) : null}
    </Box>
  );
}

export default SocialsToolbar;
