import { forwardRef, ReactNode } from 'react';

import { Box } from '@mui/material';
import { StyledRootProps } from '@packages/types';

import {
  WithSectionTitleWrapper,
  WithSectionTitleWrapperProps,
} from '../../layouts/WithSectionTitleWrapper';

export interface HomeReviewsSectionProps
  extends StyledRootProps,
    Omit<WithSectionTitleWrapperProps, 'children'> {
  children?: ReactNode;
}

export const HomeReviewsSection = forwardRef<HTMLDivElement, HomeReviewsSectionProps>(
  (props, ref) => {
    const { title, subtitle, maxWidth = 'lg', titleProps, children } = props;

    return (
      <Box ref={ref} py={10} id="review">
        <WithSectionTitleWrapper
          title={title}
          subtitle={subtitle}
          maxWidth={maxWidth}
          titleProps={titleProps}
        >
          {children}
        </WithSectionTitleWrapper>
      </Box>
    );
  },
);

HomeReviewsSection.displayName = 'HomeReviewsSection';
