import { forwardRef } from 'react';

import { Box, BoxProps, useTheme } from '@mui/material';

export interface SpacerProps {
  sx?: BoxProps['sx'];
  xSpacing?: number; // multiplier of theme.spacing
  ySpacing?: number; // multiplier of theme.spacing
  basis?: number; // multiplier of theme.spacing
}

export const Spacer = forwardRef<HTMLDivElement, SpacerProps>((props, ref) => {
  const { xSpacing, ySpacing, basis, sx } = props;

  const theme = useTheme();

  return (
    <Box
      ref={ref}
      width={xSpacing ? theme.spacing(xSpacing) : undefined}
      height={ySpacing ? theme.spacing(ySpacing) : undefined}
      flexBasis={basis ? theme.spacing(basis) : undefined}
      flexGrow={0}
      flexShrink={0}
      sx={sx}
    />
  );
});

Spacer.displayName = 'Spacer';
