import { forwardRef } from 'react';

import { Box } from '@mui/material';
import { StyledRootProps, WithRootProps } from '@packages/types';
import Link, { LinkProps } from 'components/links/Link';
import { IcoMoon } from 'custom/IcoMoon';
import WithTitleTemplate from 'templates/WithTitleTemplate';
import { theme } from 'theme/theme';
import { SocialsToolbar, SocialsToolbarProps } from 'views/SocialsToolbar';

export type ContactsInfoViewProps = WithRootProps<
  StyledRootProps,
  {
    phone?: string;
    email?: string;
    location?: string;
    mediaLinks?: SocialsToolbarProps;
  }
>;

export const ContactsInfoView = forwardRef<HTMLDivElement, ContactsInfoViewProps>((props, ref) => {
  const { phone, email, location, mediaLinks, sx, className } = props;

  const defaultLinkProps: Partial<LinkProps> = {
    sx: {
      textDecoration: 'none',
      color: theme.palette.text.primary,
    },
  };

  return (
    <Box
      sx={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start', gap: 3, ...sx }}
      className={className}
      ref={ref}
    >
      {phone ? (
        <Link {...defaultLinkProps} href={`tel:${phone}`}>
          <WithTitleTemplate title={phone}>
            <IcoMoon icon="phone" />
          </WithTitleTemplate>
        </Link>
      ) : null}
      {email ? (
        <Link {...defaultLinkProps} href={`mailto:${email}`}>
          <WithTitleTemplate title={email}>
            <IcoMoon icon="email" />
          </WithTitleTemplate>
        </Link>
      ) : null}
      {location ? (
        <Link {...defaultLinkProps} href={`http://maps.google.com/?q=${location}`} target="_blank">
          <WithTitleTemplate title={location}>
            <IcoMoon icon="pin" />
          </WithTitleTemplate>
        </Link>
      ) : null}

      <SocialsToolbar {...mediaLinks} />
    </Box>
  );
});

ContactsInfoView.displayName = 'ContactsInfoView';
