import { FC, ReactNode } from 'react';

import { useGetWebsite } from 'hooks/index';
import get from 'lodash/get';
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';

export const CaptchaProvider: FC<{ children: ReactNode }> = (props) => {
  const { children } = props;
  const { data: websiteData } = useGetWebsite();

  const captchaKey = get(websiteData, 'captcha_key') || '';

  return <GoogleReCaptchaProvider reCaptchaKey={captchaKey}>{children}</GoogleReCaptchaProvider>;
};
