import { FC } from 'react';

import { Builder, Element, useNode } from '@king-david-it/builder';
import { Grid, GridProps } from '@mui/material';

import { EmptyElement } from './EmptyElement';
import { getChoicesFromArray } from './helpers/getChoicesFromArray';
import { ComponentSpec } from './types';

export const OneColumnElement: FC<GridProps> = (props) => {
  const { children, xs, sm, lg, justifyContent, alignItems } = props;

  const {
    connectors: { connect },
  } = useNode();

  return (
    <Grid
      ref={(el) => el && connect(el)}
      item
      xs={xs}
      sm={sm}
      lg={lg}
      container={Boolean(justifyContent || alignItems)}
      justifyContent={justifyContent}
      alignItems={alignItems}
    >
      {children || <EmptyElement />}
    </Grid>
  );
};

const columnsChoices = getChoicesFromArray(
  Array(12)
    .fill(null)
    .map((_, i) => String(i + 1)),
);

Builder.registerComponent<ComponentSpec>(OneColumnElement, {
  name: 'OneColumnElement',
  displayName: 'Column',
  isCanvas: true,
  hideFromInsertMenu: true,
  inputs: [
    {
      name: 'xs',
      displayName: 'Mobile size',
      type: 'select',
      choices: columnsChoices,
      defaultValue: 12,
      helperText: 'Columns follow 12 grid column system. In one row can be in total 12 columns',
    },
    {
      name: 'sm',
      displayName: 'Tablet size',
      type: 'select',
      choices: columnsChoices,
      defaultValue: 6,
    },
    {
      name: 'lg',
      displayName: 'Desktop size',
      type: 'select',
      choices: columnsChoices,
      defaultValue: 6,
    },
    {
      name: 'justifyContent',
      displayName: 'Justify content',
      type: 'radio',
      choices: getChoicesFromArray([
        'flex-start',
        'center',
        'flex-end',
        'space-between',
        'space-around',
      ]),
    },
    {
      name: 'alignItems',
      displayName: 'Align items',
      type: 'radio',
      choices: getChoicesFromArray(['flex-start', 'center', 'flex-end']),
    },
  ],
});

export const ColumnsElement: FC<GridProps & { columnsCount: number | string }> = (props) => {
  const { spacing, justifyContent, alignItems, columnsCount } = props;

  const {
    connectors: { connect },
  } = useNode();

  return (
    <Grid
      ref={(el) => el && connect(el)}
      container
      spacing={Number(spacing) || 0}
      justifyContent={justifyContent}
      alignItems={alignItems}
    >
      {Array(Number(columnsCount))
        .fill(null)
        .map((_, index) => {
          // @ts-expect-error why types are not compatible?
          return <Element key={index} id={`column_${index + 1}`} is={OneColumnElement} />;
        })}
    </Grid>
  );
};

Builder.registerComponent<ComponentSpec>(ColumnsElement, {
  name: 'ColumnsElement',
  displayName: 'Columns',
  icon: '/icons/columns.svg',
  inputs: [
    { name: 'columnsCount', type: 'select', choices: columnsChoices, defaultValue: 2 },
    { name: 'spacing', type: 'select', choices: columnsChoices, defaultValue: 0 },
    {
      name: 'justifyContent',
      type: 'radio',
      choices: getChoicesFromArray([
        'flex-start',
        'center',
        'flex-end',
        'space-between',
        'space-around',
      ]),
      defaultValue: 'flex-start',
    },
    {
      name: 'alignItems',
      type: 'radio',
      choices: getChoicesFromArray(['flex-start', 'center', 'flex-end']),
      defaultValue: 'flex-start',
    },
  ],
});
