import { FC } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { Spacer, SpacerProps } from 'custom/Spacer';

import { ComponentSpec } from './types';

export const SpacerElement: FC<Pick<SpacerProps, 'ySpacing'>> = (props) => {
  const { ySpacing } = props;

  const {
    connectors: { connect },
  } = useNode();

  return <Spacer ySpacing={Number(ySpacing)} ref={(el) => el && connect(el)} />;
};

Builder.registerComponent<ComponentSpec>(SpacerElement, {
  name: 'SpacerElement',
  displayName: 'Spacer',
  inputs: [
    {
      name: 'ySpacing',
      displayName: 'Height',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
      defaultValue: 5,
      helperText: 'Each point is 8px, for example 1.5 * 8 = 12px',
    },
  ],
});
