import { FC } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { Identifier } from '@packages/types';
import { DEFAULT_LOCALE } from 'application/constants';
import { LISTINGS_RESOURCE } from 'application/resources';
import { SingleListingsCarouselSection } from 'components/sections/SingleListingsCarouselSection';
import { useLocaleContext } from 'contexts/LocaleContext';
import get from 'lodash/get';

import { ComponentSpec } from '../types';

export interface SingleListingsCarouselSectionElementProps {
  buttonText: string;
  ids: Identifier[];
}

export const SingleListingsCarouselSectionElement: FC<SingleListingsCarouselSectionElementProps> = (
  props,
) => {
  const { buttonText: buttonTextProp, ids } = props;

  const {
    connectors: { connect },
  } = useNode();

  const { locale } = useLocaleContext();

  const buttonText = get(buttonTextProp, locale) || get(buttonTextProp, DEFAULT_LOCALE);

  return (
    <SingleListingsCarouselSection
      ref={(el) => {
        el?.addEventListener('click', () => {
          // Create a new mousedown event
          const mouseDownEvent = new MouseEvent('mousedown', {
            view: window,
          });

          // Dispatch the mousedown event on the element
          el.dispatchEvent(mouseDownEvent);
        });

        if (el) {
          connect(el);
        }
      }}
      buttonText={buttonText}
      ids={ids}
    />
  );
};

Builder.registerComponent<ComponentSpec>(SingleListingsCarouselSectionElement, {
  name: 'SingleListingsCarouselSectionElement',
  displayName: 'Single listings section',
  inputs: [
    {
      name: 'buttonText',
      displayName: 'Button text',
      type: 'string',
      defaultValue: 'Hot deal',
      isTranslatable: true,
    },
    {
      name: 'ids',
      displayName: 'Listings ids',
      type: 'referenceAutocomplete',
      multiple: true,
      options: {
        reference: LISTINGS_RESOURCE,
        optionText: 'title',
      },
    },
  ],
});
