import { FC } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { Box, Container, ContainerProps } from '@mui/material';

import { EmptyElement } from './EmptyElement';
import { getChoicesFromArray } from './helpers/getChoicesFromArray';
import { ComponentSpec } from './types';

export type ContainerElementProps = ContainerProps;

export const ContainerElement: FC<ContainerElementProps> = (props) => {
  const {
    connectors: { connect },
  } = useNode();

  const { children } = props;

  return (
    <Box ref={(el: HTMLElement) => el && connect(el)}>
      <Container {...props}>{children || <EmptyElement />}</Container>
    </Box>
  );
};

Builder.registerComponent<ComponentSpec>(ContainerElement, {
  name: 'ContainerElement',
  displayName: 'Container',
  icon: '/icons/container.svg',
  isCanvas: true,
  inputs: [
    {
      name: 'maxWidth',
      displayName: 'Max width',
      defaultValue: 'lg',
      type: 'select',
      choices: getChoicesFromArray(['xs', 'sm', 'md', 'lg', 'xl']),
    },
  ],
});
