import { ComponentType, forwardRef, useCallback } from 'react';

import { Box, Theme, useMediaQuery } from '@mui/material';
import { useMutation } from '@tanstack/react-query';
import { ButtonElementProps } from 'builder/elements/ButtonElement';
import Button from 'buttons/Button';
import { useForm } from 'hooks/useForm';
import { useSnackbar } from 'hooks/useSnackbar';
import { useTranslate } from 'hooks/useTranslate';
import { PhoneInput } from 'inputs/PhoneInput';
import { TextInput } from 'inputs/TextInput';
import { useGoogleReCaptcha } from 'react-google-recaptcha-v3';
import { GridContainer } from 'templates/GridContainer';
import { GridItem } from 'templates/GridItem';
import { FormView } from 'views/FormView';

import { contact } from './api';
import { SOURCES } from './constants';
import { ContactFormData, contactFormValidationSchema } from './schema';

export interface ContactFormProps {
  BuilderButton?: ComponentType<ButtonElementProps>;
}

export const ContactForm = forwardRef<HTMLDivElement, ContactFormProps>((props, ref) => {
  const { BuilderButton } = props;

  const { executeRecaptcha } = useGoogleReCaptcha();

  const form = useForm({
    validationSchema: contactFormValidationSchema,
  });

  const { reset } = form;

  const translate = useTranslate({ basePath: 'forms.contact' });
  const { enqueueSnackbar } = useSnackbar();

  const isDesktop = useMediaQuery<Theme>((theme) => theme.breakpoints.up('md'));

  const handleSuccessfulSubmit = useCallback(() => {
    enqueueSnackbar(translate('success_message'), { variant: 'success' });
    reset();
  }, [reset, enqueueSnackbar, translate]);

  const { mutateAsync, isLoading } = useMutation({
    mutationFn: contact,
    onSuccess: handleSuccessfulSubmit,
  });

  const handleSubmit = useCallback(
    async (data: ContactFormData) => {
      await mutateAsync({ data, executeRecaptcha });
    },
    [mutateAsync, executeRecaptcha],
  );

  return (
    <Box ref={ref}>
      <FormView {...form} onSubmit={handleSubmit}>
        <GridContainer>
          <GridItem md={6}>
            <TextInput name={SOURCES['first_name']} label={translate('first_name')} />
          </GridItem>
          <GridItem md={6}>
            <TextInput name={SOURCES['last_name']} label={translate('last_name')} />
          </GridItem>
          <GridItem md={6}>
            <TextInput name={SOURCES['email']} type="email" label={translate('email')} />
          </GridItem>
          <GridItem md={6}>
            <PhoneInput name={SOURCES['phone']} label={translate('phone')} />
          </GridItem>
          <GridItem>
            <TextInput
              name={SOURCES['note']}
              multiline
              label={translate('note')}
              textFieldProps={{ rows: 3 }}
            />
          </GridItem>
        </GridContainer>

        {BuilderButton ? (
          <BuilderButton
            sx={{ mt: 2 }}
            type="submit"
            loading={isLoading}
            // Default text
            text={{
              'en-US': 'Send',
              'fr-FR': 'Envoyer',
              'he-IL': '',
            }}
          />
        ) : (
          <Button
            variant="contained"
            sx={{ mt: 2 }}
            type="submit"
            fullWidth={!isDesktop}
            loading={isLoading}
          >
            {translate.root('buttons.send')}
          </Button>
        )}
      </FormView>
    </Box>
  );
});

ContactForm.displayName = 'ContactForm';

export default ContactForm;
