import { Children, FC, useCallback, useState } from 'react';

import { Box, IconButton, IconButtonProps } from '@mui/material';
import { clsx } from 'clsx';
import { IcoMoon } from 'custom/IcoMoon';
import SwiperCore from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';
import { Swiper, SwiperProps, SwiperSlide } from 'swiper/react';
import { styled } from 'theme/index';
const PREFIX = 'SingleListingSwiper';

export const SingleListingSwiperClasses = {
  navigationButtonWrapper: `${PREFIX}-navigationButtonWrapper`,
  disabledNavigationButtonWrapper: `${PREFIX}-disabledNavigationButtonWrapper`,
  prevButtonWrapper: `${PREFIX}-prevButtonWrapper`,
  nextButtonWrapper: `${PREFIX}-nextButtonWrapper`,
  navigationButton: `${PREFIX}-navigationButton`,
  prevButton: `${PREFIX}-prevButton`,
  nextButton: `${PREFIX}-nextButton`,

  // swiper core and modules classes, add others if need
  paginationContainer: 'swiper-pagination',
  paginationBullet: 'swiper-pagination-bullet',
  paginationFraction: 'swiper-pagination-fraction',
  paginationActiveBullet: 'swiper-pagination-bullet-active',
};

export const StyledRoot = styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  const isRtl = theme.direction === 'rtl';

  return {
    position: 'relative',
    [`& .${SingleListingSwiperClasses.navigationButtonWrapper}`]: {
      position: 'absolute',
      top: '50%',
      transform: 'translateY(-50%)',
      zIndex: 2,
      transition: '0.3s',
    },
    [`& .${SingleListingSwiperClasses.disabledNavigationButtonWrapper}`]: {
      [`& .${SingleListingSwiperClasses.navigationButton}`]: {
        visibility: 'hidden',
      },
    },
    [`& .${SingleListingSwiperClasses.prevButtonWrapper}`]: {
      left: theme.spacing(1),
    },
    [`& .${SingleListingSwiperClasses.nextButtonWrapper}`]: {
      right: theme.spacing(1),
    },
    [`& .${SingleListingSwiperClasses.navigationButton}`]: {
      [theme.breakpoints.up('md')]: {
        opacity: 0.8,
      },
      backgroundColor: theme.palette.primary.contrastText,
      '& svg': {
        color: theme.palette.text.light,
      },
      ':hover': {
        opacity: 1,
        backgroundColor: theme.palette.primary.contrastText,
      },
    },

    [`& .${SingleListingSwiperClasses.prevButton}`]: {
      transform: isRtl ? 'rotate(180deg)' : 'rotate(0deg)',
    },

    [`& .${SingleListingSwiperClasses.nextButton}`]: {
      transform: isRtl ? 'rotate(0deg)' : 'rotate(180deg)',
    },

    [`& .${SingleListingSwiperClasses.paginationContainer}`]: {
      lineHeight: 0,
    },
    [`& .${SingleListingSwiperClasses.paginationBullet}`]: {
      width: 6,
      height: 6,
      opacity: 0.6,
      backgroundColor: theme.palette.primary.contrastText,
    },
    [`& .${SingleListingSwiperClasses.paginationFraction}`]: {
      bottom: 24,
    },
    [`& .${SingleListingSwiperClasses.paginationActiveBullet}`]: {
      opacity: 1,
    },
  };
});

const ArrowButton = (props: Omit<IconButtonProps, 'children'>) => {
  const { className, ...rest } = props;

  return (
    <IconButton
      size="small"
      className={clsx(SingleListingSwiperClasses.navigationButton)}
      {...rest}
    >
      <IcoMoon icon="chevron-left" size="small" className={className} />
    </IconButton>
  );
};

export const SingleListingSwiper: FC<SwiperProps> = (props) => {
  const { children } = props;

  const {
    className: swiperClassName,
    navigation = false,
    onResize,
    onBeforeInit,
    onAfterInit,
    onSlideChange,
    pagination,
    loop,
    ...restSwiperBaseProps
  } = props || {};

  const isNavigationEnabled = Boolean(navigation);
  const navigationParams = typeof navigation !== 'boolean' ? navigation : {};

  const [isFirstGroup, setIsFirstGroup] = useState(false);
  const [isLastGroup, setIsLastGroup] = useState(false);

  const [prevElement, setPrevElement] = useState<HTMLDivElement | null>();
  const [nextElement, setNextElement] = useState<HTMLDivElement | null>();

  const setGroupsStatus = useCallback((swiper: SwiperCore) => {
    const { isBeginning, isEnd } = swiper;

    setIsFirstGroup(isBeginning);
    setIsLastGroup(isEnd);
  }, []);

  const handleResize = useCallback<NonNullable<SwiperProps['onResize']>>(
    (swiper) => {
      onResize?.(swiper);
      setGroupsStatus(swiper);
    },
    [onResize, setGroupsStatus],
  );

  const handleAfterInit = useCallback<NonNullable<SwiperProps['onAfterInit']>>(
    (swiper) => {
      onAfterInit?.(swiper);
      setGroupsStatus(swiper);
    },
    [onAfterInit, setGroupsStatus],
  );

  const handleSlideChange = useCallback<NonNullable<SwiperProps['onSlideChange']>>(
    (swiper) => {
      onSlideChange?.(swiper);
      setGroupsStatus(swiper);
    },
    [onSlideChange, setGroupsStatus],
  );

  /**
   * We need to assign custom navigation elements before swiper init
   * because they don't work when we just pass it to navigation options
   */
  const handleBeforeInit = useCallback<NonNullable<SwiperProps['onBeforeInit']>>(
    (swiper) => {
      const navigation = swiper.params.navigation;

      if (navigation && typeof navigation !== 'boolean') {
        navigation.prevEl = prevElement;
        navigation.nextEl = nextElement;
      }
    },
    [nextElement, prevElement],
  );

  return (
    <StyledRoot>
      {isNavigationEnabled && (
        <Box
          ref={setPrevElement}
          className={clsx(
            SingleListingSwiperClasses.navigationButtonWrapper,
            SingleListingSwiperClasses.prevButtonWrapper,
          )}
        >
          <ArrowButton
            className={SingleListingSwiperClasses.prevButton}
            disabled={!loop && isFirstGroup}
          />
        </Box>
      )}

      <Swiper
        className={swiperClassName}
        modules={[Navigation, Pagination]}
        navigation={
          isNavigationEnabled
            ? {
                prevEl: prevElement,
                nextEl: nextElement,
                disabledClass: SingleListingSwiperClasses.disabledNavigationButtonWrapper,
                ...navigationParams,
              }
            : false
        }
        pagination={{
          clickable: true,
          enabled: !!pagination,
          dynamicBullets: true,
          dynamicMainBullets: 3,
        }}
        onBeforeInit={handleBeforeInit}
        onAfterInit={handleAfterInit}
        onResize={handleResize}
        onSlideChange={handleSlideChange}
        loop={loop}
        {...restSwiperBaseProps}
      >
        {Children.map(children, (child, index) => {
          return <SwiperSlide key={index}>{child}</SwiperSlide>;
        })}
      </Swiper>

      {isNavigationEnabled && (
        <Box
          ref={setNextElement}
          className={clsx(
            SingleListingSwiperClasses.navigationButtonWrapper,
            SingleListingSwiperClasses.nextButtonWrapper,
          )}
        >
          <ArrowButton
            className={SingleListingSwiperClasses.nextButton}
            disabled={!loop && isLastGroup}
          />
        </Box>
      )}
    </StyledRoot>
  );
};
