import { FC } from 'react';

import { Fab, useTheme } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { IcoMoon } from 'custom/IcoMoon';
import { useGetWebsite } from 'hooks/index';

const getWhatsAppLink = (phone: string) => {
  // remove all not digit characters from phone string
  const sanitarizedPhoneNumber = phone.replace(/\D/g, '');
  return `https://wa.me/${sanitarizedPhoneNumber}`;
};

export const WhatsAppFloatButton: FC<StyledRootProps> = (props) => {
  const { sx, className } = props;

  const theme = useTheme();
  const { data: website } = useGetWebsite();

  if (!website?.phone) {
    return null;
  }

  return (
    <Fab
      variant="circular"
      color="success"
      href={getWhatsAppLink(website.phone)}
      target="_blank"
      className={className}
      sx={{
        position: 'fixed',
        bottom: '1.5rem',
        right: { xs: '1rem', sm: '1.5rem' },
        bgcolor: 'success.main',
        ...sx,
      }}
    >
      <IcoMoon icon="whatsapp" size="large" color={theme.palette.primary.contrastText} />
    </Fab>
  );
};

export default WhatsAppFloatButton;
