import { forwardRef } from 'react';

import { Box } from '@mui/material';
import { ContactForm, ContactFormProps } from 'components/forms/ContactForm';
import {
  WithSectionTitleWrapper,
  WithSectionTitleWrapperProps,
} from 'components/layouts/WithSectionTitleWrapper';

export type ContactSectionProps = WithSectionTitleWrapperProps & ContactFormProps;

export const ContactSection = forwardRef<HTMLDivElement, ContactSectionProps>((props, ref) => {
  const { title, subtitle, maxWidth, BuilderButton } = props;

  return (
    <Box ref={ref} sx={{ bgcolor: 'background.paper', py: 10 }}>
      <WithSectionTitleWrapper title={title} subtitle={subtitle} maxWidth={maxWidth}>
        <ContactForm BuilderButton={BuilderButton} />
      </WithSectionTitleWrapper>
    </Box>
  );
});

ContactSection.displayName = 'ContactSection';
