import { FC, ReactNode, useEffect, useState } from 'react';

import { alpha, Box, CircularProgress, generateUtilityClasses, GlobalStyles } from '@mui/material';
import Router from 'next/router';
import { styled } from 'theme/index';

interface PageLoaderProps {
  children: ReactNode;
}

const PREFIX = 'PageLoader';

export const PageLoaderClasses = generateUtilityClasses(PREFIX, ['root', 'loader']);

const StyledRoot = styled(Box, {
  name: `Styled--${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${PageLoaderClasses.root}`]: {
      position: 'fixed',
      zIndex: 2000, // higher than anything else
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      overflow: 'hidden',
      backgroundColor: alpha(theme.palette.text.primary, 0.6),
    },

    [`& .${PageLoaderClasses.loader}`]: {
      color: theme.palette.common.white,
    },
  };
});

// prevent page scrolling
const bodyHiddenOverflowStyles = <GlobalStyles styles={{ body: { overflow: 'hidden' } }} />;

export const PageLoader: FC<PageLoaderProps> = (props) => {
  const { children } = props;

  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    const handleStart = (_: unknown, options: { shallow?: boolean }) => {
      if (!options?.shallow) {
        setIsLoading(true);
      }
    };

    const handleComplete = () => {
      setIsLoading(false);
    };

    const handleError = () => {
      setIsLoading(false);
    };

    Router.events.on('routeChangeStart', handleStart);
    Router.events.on('routeChangeComplete', handleComplete);
    Router.events.on('routeChangeError', handleError);

    return () => {
      Router.events.off('routeChangeStart', handleStart);
      Router.events.off('routeChangeComplete', handleComplete);
      Router.events.off('routeChangeError', handleError);
    };
  }, []);

  return (
    <>
      {isLoading && (
        <StyledRoot className={PageLoaderClasses.root}>
          {bodyHiddenOverflowStyles}
          <CircularProgress size={96} className={PageLoaderClasses.loader} />
        </StyledRoot>
      )}
      {children}
    </>
  );
};
