import { FC, ReactNode } from 'react';

import { Box, Breakpoint, Container, Grid } from '@mui/material';
import { Typography, TypographyProps } from 'components/Typography';

export interface WithSectionTitleWrapperProps {
  title?: ReactNode;
  subtitle?: ReactNode;
  children: ReactNode;
  maxWidth?: Breakpoint;
  titleProps?: TypographyProps;
}

export const WithSectionTitleWrapper: FC<WithSectionTitleWrapperProps> = (props) => {
  const { title, subtitle, children, maxWidth = 'xl', titleProps } = props;

  return (
    <Box>
      <Container maxWidth={maxWidth}>
        <Box mb={6}>
          {typeof title === 'string' ? (
            <Typography
              variant="h3"
              mb={1}
              textAlign="center"
              sx={{ wordBreak: 'break-word' }}
              {...titleProps}
            >
              {title}
            </Typography>
          ) : (
            <>{title}</>
          )}

          {subtitle && (
            <Grid container justifyContent="center">
              <Grid item maxWidth={464} textAlign="center">
                {subtitle}
              </Grid>
            </Grid>
          )}
        </Box>

        {children}
      </Container>
    </Box>
  );
};
