import { Grid, Typography } from '@mui/material';
import { useTranslation } from 'next-i18next';

/**
 * Empty Element is used when there's no children in other elements like box or column.
 */
export const EmptyElement = () => {
  const { t } = useTranslation('common');

  return (
    <Grid container justifyContent="center" alignItems="center" minHeight={80}>
      <Typography variant="body2">+ {t('builder.add_block')}</Typography>
    </Grid>
  );
};
