import { FC } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { Box, BoxProps } from '@mui/material';

import { EmptyElement } from './EmptyElement';
import { ComponentSpec } from './types';

export const BoxElement: FC<BoxProps & { backgroundColor: string }> = (props) => {
  const { children, backgroundColor, pt, pb, pl, pr, mt, mb, ml, mr } = props;

  const {
    connectors: { connect },
  } = useNode();

  return (
    <Box
      sx={{
        pt: Number(pt),
        pb: Number(pb),
        pl: Number(pl),
        pr: Number(pr),
        mt: Number(mt),
        mb: Number(mb),
        ml: Number(ml),
        mr: Number(mr),
        backgroundColor,
      }}
      ref={(el: HTMLDivElement) => el && connect(el)}
    >
      {children || <EmptyElement />}
    </Box>
  );
};

Builder.registerComponent<ComponentSpec>(BoxElement, {
  name: 'BoxElement',
  icon: '/icons/box.svg',
  displayName: 'Box',
  isCanvas: true,
  inputs: [
    { name: 'backgroundColor', type: 'color' },
    {
      name: 'pt',
      displayName: 'Padding top',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
      helperText: 'Each point is 8px, for example 1.5 * 8 = 12px',
    },
    {
      name: 'pb',
      displayName: 'Padding bottom',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
    {
      name: 'pl',
      displayName: 'Padding left',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
    {
      name: 'pr',
      displayName: 'Padding right',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
    {
      name: 'mt',
      displayName: 'Margin top',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
    {
      name: 'mb',
      displayName: 'Margin bottom',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
    {
      name: 'ml',
      displayName: 'Margin left',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
    {
      name: 'mr',
      displayName: 'Margin right',
      type: 'number',
      min: 0,
      step: 0.5,
      max: 100,
    },
  ],
});
