import { forwardRef, ReactNode } from 'react';

import { Box } from '@mui/material';
import {
  WithSectionTitleWrapper,
  WithSectionTitleWrapperProps,
} from 'components/layouts/WithSectionTitleWrapper';
import { SocialsToolbarProps } from 'views/SocialsToolbar';

export interface TeamMember {
  name: string;
  position: string;
  avatarSrc?: string;
  mediaLinks?: Partial<SocialsToolbarProps>;
}

export interface TeamSectionProps extends WithSectionTitleWrapperProps {
  children: ReactNode;
}

export const TeamSection = forwardRef<HTMLDivElement, TeamSectionProps>((props, ref) => {
  const { title, subtitle, maxWidth = 'lg', children } = props;

  return (
    <Box ref={ref} py={10}>
      <WithSectionTitleWrapper title={title} subtitle={subtitle} maxWidth={maxWidth}>
        {children}
      </WithSectionTitleWrapper>
    </Box>
  );
});

TeamSection.displayName = 'TeamSection';
