import { FC } from 'react';

import { Builder, Element, useNode } from '@king-david-it/builder';
import { ContactSection, ContactSectionProps } from 'components/sections/ContactSection';
import { useTranslate } from 'hooks/index';
import { merge } from 'lodash';

import { ButtonElement, ButtonElementProps } from '../ButtonElement';
import { getChoicesFromArray } from '../helpers/getChoicesFromArray';
import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

export interface ContactSectionElementProps
  extends Omit<ContactSectionProps, 'title' | 'subtitle'> {
  title?: Record<string, string>;
  subtitle?: Record<string, string>;
}

const ContactTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ wordBreak: 'break-word', ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(ContactTextElement, {
  ...textElementSettings,
  name: 'ContactTextElement',
});

export const ContactSectionElement: FC<ContactSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp } = props;
  const {
    connectors: { connect },
  } = useNode();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });

  return (
    <ContactSection
      ref={(el) => el && connect(el)}
      {...props}
      title={
        <Element
          id="contact_section_element_title"
          // @ts-expect-error why types are not compatible?
          is={ContactTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_title'),
              'fr-FR': t('text_element_title'),
              'he-IL': t('text_element_title'),
            },
            titleProp,
          )}
          align="center"
          variant="h3"
          component="h3"
        />
      }
      subtitle={
        <Element
          id="contact_section_element_subtitle"
          // @ts-expect-error why types are not compatible?
          is={ContactTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_subtitle'),
              'fr-FR': t('text_element_subtitle'),
              'he-IL': t('text_element_subtitle'),
            },
            subtitleProp,
          )}
          align="center"
          variant="subtitle1"
        />
      }
      BuilderButton={(props: ButtonElementProps) => (
        <Element id="contact_section_submit_button" is={ButtonElement} {...props} />
      )}
    />
  );
};

Builder.registerComponent<ComponentSpec>(ContactSectionElement, {
  name: 'ContactSectionElement',
  displayName: 'Contact section',
  inputs: [
    {
      name: 'maxWidth',
      displayName: 'Max width',
      type: 'select',
      choices: getChoicesFromArray(['sm', 'md', 'lg', 'xl']),
      defaultValue: 'sm',
    },
  ],
});
