import { FC, useCallback, useEffect, useState } from 'react';

import { Builder, Element, useBuilderState, useNode } from '@king-david-it/builder';
import { Box, Chip, Grid, Theme, Typography, useMediaQuery } from '@mui/material';
import { Identifier } from '@packages/types';
import {
  ACCOMMODATES_SOURCE,
  BASE_PRICE_SLUG,
  BATHROOMS_SOURCE,
  BEDROOMS_SOURCE,
  DEFAULT_LOCALE,
  listingRoute,
  RATING_COUNT_SLUG,
  RATING_SLUG,
} from 'application/constants';
import { LISTINGS_RESOURCE } from 'application/resources';
import {
  WithSectionTitleWrapper,
  WithSectionTitleWrapperProps,
} from 'components/layouts/WithSectionTitleWrapper';
import { useLocaleContext } from 'contexts/LocaleContext';
import { Loader } from 'custom/Loader';
import getFilterValues from 'helpers/getFilterValues';
import { useGetPriceInCurrentCurrency } from 'helpers/useGetPriceInCurrentCurrency';
import { useTranslate } from 'hooks/index';
import { useGetListings } from 'hooks/queries/useGetListings';
import { merge } from 'lodash';
import get from 'lodash/get';
import { useRouter } from 'next/router';
import ListingItem from 'views/ListingItem';
import { SectionSwiper } from 'views/SectionSwiper';

import { getChoicesFromArray } from '../helpers/getChoicesFromArray';
import {
  ListingButtonsContextProvider,
  useListingButtonsContext,
  useListingButtonsController,
} from '../helpers/ListingButtonsContext';
import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

export interface ListingsCarouselSectionElementProps
  extends Omit<WithSectionTitleWrapperProps, 'title' | 'subtitle'> {
  buttonsCount: string;
  ids: Identifier[];
  title?: Record<string, string>;
  subtitle?: Record<string, string>;
}

interface CarouselChipElementProps {
  text: string;
  index: number;
}

const LISTING_ITEM_DISPLAY_NAME = 'Listing item';

const CarouselChipElement: FC<CarouselChipElementProps> = (props) => {
  const { text: textProp, index } = props;
  const { activeIndex } = useListingButtonsContext();

  const {
    connectors: { connect },
  } = useNode();

  const { locale } = useLocaleContext();

  const text = get(textProp, locale) || get(textProp, DEFAULT_LOCALE);

  return (
    <Box ref={(el: HTMLDivElement) => connect(el)}>
      <Chip
        clickable
        variant={index === activeIndex ? 'filled' : 'outlined'}
        color="primary"
        label={text}
      />
    </Box>
  );
};

Builder.registerComponent<ComponentSpec>(CarouselChipElement, {
  name: 'CarouselChipElement',
  displayName: 'Button',
  inputs: [
    {
      name: 'text',
      displayName: 'Button name',
      type: 'string',
      defaultValue: 'Button name...',
      isTranslatable: true,
    },
  ],
});

interface ListingItemElementProps {
  ids: Identifier[];
  listingNodeId: string;
}

const ListingItemElement: FC<ListingItemElementProps> = (props) => {
  const { ids, listingNodeId } = props;

  const { setListings } = useListingButtonsContext();
  const getPrice = useGetPriceInCurrentCurrency();
  const { query } = useRouter();
  const translate = useTranslate();
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const {
    connectors: { connect },
    actions: { setProp },
  } = useNode();

  const { isEditing } = useBuilderState();

  const getListingsResult = useGetListings({
    ids,
  });

  useEffect(() => {
    if (!isEditing) return;

    if (getListingsResult.isSuccess && !ids) {
      setProp((props: Record<string, unknown>) => {
        props.ids = getListingsResult.data?.map((item) => item.id).slice(0, 3) || [];
      });
    }
  }, [getListingsResult.data, getListingsResult.isSuccess, ids, isEditing, setProp]);

  useEffect(() => {
    setListings(ids, listingNodeId);
  }, [ids, listingNodeId, setListings]);

  if (getListingsResult.isLoading) {
    return <Loader />;
  }

  return (
    <Box
      ref={(el: HTMLDivElement) => {
        el?.addEventListener('click', () => {
          // Create a new mousedown event
          const mouseDownEvent = new MouseEvent('mousedown', {
            view: window,
          });

          // Dispatch the mousedown event on the element
          el.dispatchEvent(mouseDownEvent);
        });

        if (el) {
          connect(el);
        }
      }}
    >
      {ids && !ids?.length ? (
        <Typography variant="h3" textAlign="center">
          {translate.root('custom.click_to_choose_listings')}
        </Typography>
      ) : (
        <>
          {getListingsResult.isSuccess && (
            <SectionSwiper slidesPerView={isMobile ? 1 : 3} centerInsufficientSlides={true}>
              {getListingsResult.data.map((listingData, index) => {
                const [accommodates, bedrooms, bathrooms, basePrice, rating, ratingCount] =
                  getFilterValues({
                    formattedFilters: listingData.formatted_filters,
                    slugs: [
                      ACCOMMODATES_SOURCE,
                      BEDROOMS_SOURCE,
                      BATHROOMS_SOURCE,
                      BASE_PRICE_SLUG,
                      RATING_SLUG,
                      RATING_COUNT_SLUG,
                    ],
                  });

                const { currency, title, address } = listingData;

                const parsedBasePrice = basePrice?.value ? Number(basePrice?.value) : 0;

                const priceValue = getPrice({
                  initialCurrencyCode: currency?.code || '',
                  amount: parsedBasePrice,
                });

                const zip = undefined; // no BE data

                return (
                  <ListingItem
                    withTooltip
                    key={index}
                    zip={zip}
                    sx={{ flex: '0 0 auto' }}
                    name={title}
                    address={address}
                    price={priceValue}
                    pricePrefix="from"
                    rating={rating?.value}
                    reviewsCount={ratingCount?.value}
                    amenitiesItems={[
                      { id: '1', value: bedrooms?.value, label: bedrooms?.filterAttribute?.title },
                      {
                        id: '2',
                        value: bathrooms?.value,
                        label: bathrooms?.filterAttribute?.title,
                      },
                      {
                        id: '3',
                        value: accommodates?.value,
                        label: translate.root('custom.guest'),
                      },
                    ]}
                    href={{
                      pathname: listingRoute.getPathname(listingData.slug),
                      query,
                    }}
                    size="large"
                    items={listingData.gallery.map((galleryItem) => {
                      const { id, src, title } = galleryItem;
                      return {
                        id,
                        url: src,
                        title,
                      };
                    })}
                  />
                );
              })}
            </SectionSwiper>
          )}
        </>
      )}
    </Box>
  );
};

Builder.registerComponent<ComponentSpec>(ListingItemElement, {
  name: 'ListingItemElement',
  displayName: LISTING_ITEM_DISPLAY_NAME,
  inputs: [
    {
      name: 'ids',
      displayName: 'Listings ids',
      type: 'referenceAutocomplete',
      options: {
        reference: LISTINGS_RESOURCE,
        optionText: 'title',
      },
      multiple: true,
    },
  ],
});

const ListingsTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ wordBreak: 'break-word', ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(ListingsTextElement, {
  ...textElementSettings,
  name: 'ListingsTextElement',
});

export const ListingsCarouselSectionElement: FC<ListingsCarouselSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp, maxWidth = 'lg', buttonsCount } = props;
  const { activeIndex, setActiveIndex, listings, setListings } = useListingButtonsController();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });
  const [isInitialized, setIsInitialized] = useState(false);

  const onChangeIndex = useCallback(
    (index: number) => {
      setActiveIndex(index);
    },
    [setActiveIndex],
  );
  const {
    connectors: { connect },
    id,
  } = useNode();

  const { isEditing } = useBuilderState();

  const getListingNodeId = (index: number) => `listing_${id}_${index}`;

  // set initial index, when we change buttonsCount in Editor
  useEffect(() => {
    if (isEditing) {
      onChangeIndex(0);
    }
  }, [buttonsCount, isEditing, onChangeIndex]);

  // set initial index of first not empty listing
  useEffect(() => {
    const activeIndex = Object.values(listings).findIndex((el) => el?.length);

    if (!isEditing) {
      onChangeIndex(activeIndex);
    }
  }, [isEditing, listings, onChangeIndex]);

  useEffect(() => {
    /**
     * It fixes issue with slides blinking on first render with centerIssuficientSlides option
     */
    setIsInitialized(true);
  }, []);

  return (
    <Box
      ref={(el: HTMLDivElement) => el && connect(el)}
      sx={{
        py: 10,
        bgcolor: 'background.paper',
      }}
    >
      <WithSectionTitleWrapper
        title={
          <Element
            id="listings_carousel_section_element_title"
            // @ts-expect-error why types are not compatible?
            is={ListingsTextElement}
            // [PREV_VALUES_SUPPORT] - see COMMENTS.md
            text={merge(
              {
                'en-US': t('text_element_title'),
                'fr-FR': t('text_element_title'),
                'he-IL': t('text_element_title'),
              },
              titleProp,
            )}
            align="center"
            variant="h3"
            component="h3"
          />
        }
        subtitle={
          <Element
            id="listings_carousel_section_element_subtitle"
            // @ts-expect-error why types are not compatible?
            is={ListingsTextElement}
            // [PREV_VALUES_SUPPORT] - see COMMENTS.md
            text={merge(
              {
                'en-US': t('text_element_subtitle'),
                'fr-FR': t('text_element_subtitle'),
                'he-IL': t('text_element_subtitle'),
              },
              subtitleProp,
            )}
            align="center"
            variant="subtitle1"
          />
        }
        maxWidth={maxWidth}
      >
        <Box>
          <ListingButtonsContextProvider
            value={{ activeIndex, setActiveIndex, listings, setListings }}
          >
            <Grid container justifyContent="center" gap={2}>
              {Array(Number(buttonsCount))
                .fill(null)
                .map((_, index) => {
                  const listingIds = get(listings, getListingNodeId(index));

                  const isShowButton = !!(isEditing || listingIds?.length);

                  return (
                    isShowButton && (
                      <Box key={index} onClick={() => onChangeIndex(index)}>
                        {/* @ts-expect-error why types are not compatible? */}
                        <Element
                          index={index}
                          id={`button_${index + 1}`}
                          is={CarouselChipElement}
                        />
                      </Box>
                    )
                  );
                })}
            </Grid>

            <Box pt={6}>
              {Array(Number(buttonsCount))
                .fill(null)
                .map((_, index) => {
                  const isShowBlock = !isInitialized || index === activeIndex;

                  return (
                    <Box
                      display={isShowBlock ? 'block' : 'none'}
                      key={index}
                      visibility={isInitialized ? 'visible' : 'hidden'}
                    >
                      {/* @ts-expect-error why types are not compatible? */}
                      <Element
                        listingNodeId={getListingNodeId(index)}
                        id={`listing_item_${index + 1}`}
                        is={ListingItemElement}
                      />
                    </Box>
                  );
                })}
            </Box>
          </ListingButtonsContextProvider>
        </Box>
      </WithSectionTitleWrapper>
    </Box>
  );
};

const buttonsChoices = getChoicesFromArray(
  Array(6)
    .fill(null)
    .map((_, i) => String(i + 1)),
);
Builder.registerComponent<ComponentSpec>(ListingsCarouselSectionElement, {
  name: 'ListingsCarouselSectionElement',
  displayName: 'Listings carousel section',
  inputs: [
    {
      name: 'buttonsCount',
      displayName: 'Buttons count',
      type: 'select',
      choices: buttonsChoices,
      defaultValue: '3',
    },
    {
      name: 'maxWidth',
      displayName: 'Max width',
      type: 'select',
      choices: getChoicesFromArray(['sm', 'md', 'lg', 'xl']),
      defaultValue: 'lg',
    },
  ],
});
