import { forwardRef, useCallback } from 'react';

import { StyledRootProps } from '@packages/types';
import { SEARCH_ROUTE } from 'application/constants';
import { ButtonElementProps } from 'builder/elements/ButtonElement';
import { SearchListingsForm, SearchListingsFormValues } from 'components/forms/SearchListingsForm';
import useGetPopularCities from 'hooks/queries/useGetPopularCities';
import { omitBy } from 'lodash';
import Router from 'next/router';
import { ImageTemplate, ImageTemplateProps } from 'templates/ImageTemplate';
import { HEADER_HEIGHT, MOBILE_HEADER_HEIGHT } from 'theme/index';
import { SearchListingsViewProps } from 'views/SearchListingsView';

export type SearchSectionProps<A> = StyledRootProps &
  ImageTemplateProps &
  Pick<SearchListingsViewProps<A>, 'slots' | 'slotProps'>;

export const SearchSection = forwardRef<HTMLElement, SearchSectionProps<ButtonElementProps>>(
  (props, ref) => {
    const { sx, className, slots, slotProps, ...rest } = props;

    const { data: popularCitiesData = [] } = useGetPopularCities();

    const handleSubmit = useCallback(
      (values: SearchListingsFormValues) => {
        const { checkin_at, checkout_at, city: cityValue, accommodates } = values;

        const selectedPopularCity = popularCitiesData.find((city) => city.slug === cityValue);

        const filter = {
          city: cityValue,
          checkin_at,
          checkout_at,
          accommodates,
          country: selectedPopularCity?.country?.code,
          state: selectedPopularCity?.state?.code,
        };

        Router.push({
          pathname: `/${SEARCH_ROUTE}`,
          query: omitBy(filter, (value) => !value),
        });
      },
      [popularCitiesData],
    );

    return (
      <ImageTemplate
        sx={{
          display: 'flex',
          justifyContent: 'center',
          minHeight: {
            xs: `calc(100vh - ${MOBILE_HEADER_HEIGHT}px)`,
            md: `calc(100vh - ${HEADER_HEIGHT}px)`,
          },
          ...sx,
        }}
        className={className}
        {...rest}
        ref={ref}
      >
        <SearchListingsForm onSubmit={handleSubmit} slots={slots} slotProps={slotProps} />
      </ImageTemplate>
    );
  },
);

SearchSection.displayName = 'SearchSection';
