import { z } from 'zod';

import { SOURCES } from './constants';

export const contactFormValidationSchema = z.object({
  [SOURCES.first_name]: z.string().min(3).max(95).or(z.literal('')),
  [SOURCES.last_name]: z.string().min(3).max(95).or(z.literal('')),
  [SOURCES.email]: z.string().min(1).email().max(191),
  [SOURCES.phone]: z.string().max(191),
  [SOURCES.note]: z.string().min(1).max(300),
});

export type ContactFormData = z.output<typeof contactFormValidationSchema>;
