import { FC } from 'react';

import { Builder, Element, useFilesContext, useNode } from '@king-david-it/builder';
import { Grid, Theme, useMediaQuery } from '@mui/material';
import { DEFAULT_LOCALE } from 'application/constants';
import { AttributeSection, AttributeSectionProps } from 'components/sections/AttributeSection';
import { useLocaleContext } from 'contexts/LocaleContext';
import { useTranslate } from 'hooks/index';
import { get, merge } from 'lodash';
import AttributeSectionView, { AttributeSectionViewProps } from 'views/AttributeSectionView';
import { SectionSwiper } from 'views/SectionSwiper';

import { ButtonElement, ButtonElementProps } from '../ButtonElement';
import { getChoicesFromArray } from '../helpers/getChoicesFromArray';
import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

interface AttributeSectionElementProps extends Omit<AttributeSectionProps, 'title' | 'subtitle'> {
  attributesCount: string;
  title?: Record<string, string>;
  subtitle?: Record<string, string>;
}

interface AttributeElementProps extends Omit<AttributeSectionViewProps, 'attributeName'> {
  attributeName: Record<string, string>;
}

const AttributeTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ wordBreak: 'break-word', ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(AttributeTextElement, {
  ...textElementSettings,
  name: 'AttributeTextElement',
});

const AttributeButtonElement: FC<ButtonElementProps> = ({ sx, ...rest }) => {
  return (
    <ButtonElement
      sx={{
        position: 'absolute',
        bottom: 24,
        left: 24,
        py: 2,
        px: 3,
        height: 62,
        bgcolor: 'background.default',
        border: '1px solid',
        borderColor: 'primary.background',
        borderRadius: 4,
        color: 'text.primary',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        fontSize: 20,
        whiteSpace: 'nowrap',
        display: 'block',
        width: 'auto',
        maxWidth: { xs: 200, sm: 300, md: 150, lg: 200 },
        '&:hover': {
          bgcolor: 'background.default',
        },
        ...sx,
      }}
      {...rest}
    />
  );
};

const AttributeButton: FC<ButtonElementProps> = (props) => {
  return <Element id="attribute_section_button" is={AttributeButtonElement} {...props} />;
};

Builder.registerComponent<ComponentSpec>(AttributeButtonElement, {
  name: 'AttributeButtonElement',
  displayName: 'Attribute Button',
  inputs: [
    {
      name: 'text',
      displayName: 'Text',
      type: 'string',
      defaultValue: 'Enter text...',
      isTranslatable: true,
    },
    {
      name: 'borderRadius',
      displayName: 'Border radius (px)',
      type: 'number',
      min: 0,
      max: 20,
      helperText: 'Each point is 1px',
    },
  ],
});

export const AttributeElement: FC<AttributeElementProps> = (props) => {
  const { src: srcProp, attributeName: attributeNameObject, filter } = props;

  const { locale } = useLocaleContext();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });

  const {
    connectors: { connect },
  } = useNode();

  const { getFile } = useFilesContext();
  const src = getFile(srcProp)?.src;

  const text = merge(
    {
      // Default text
      'en-US': t('attribute_button'),
      'fr-FR': t('attribute_button'),
      'he-IL': t('attribute_button'),
    },
    // [PREV_VALUES_SUPPORT] - see COMMENTS.md
    attributeNameObject,
  );
  const attributeName = get(text, locale) || get(text, DEFAULT_LOCALE);

  return (
    <Grid
      item
      xs={12}
      ref={(el) => {
        el?.addEventListener('click', () => {
          // Create a new mousedown event
          const mouseDownEvent = new MouseEvent('mousedown', {
            view: window,
          });

          // Dispatch the mousedown event on the element
          el.dispatchEvent(mouseDownEvent);
        });

        if (el) {
          connect(el);
        }
      }}
      p={0.3}
    >
      <AttributeSectionView
        src={src}
        altAttributeName={attributeName}
        filter={filter}
        slots={{
          actionButton: AttributeButton,
        }}
        slotProps={{
          actionButton: { text },
        }}
      />
    </Grid>
  );
};

Builder.registerComponent<ComponentSpec>(AttributeElement, {
  name: 'AttributeElement',
  displayName: 'Attribute',
  inputs: [
    {
      name: 'src',
      displayName: 'Attribute image',
      type: 'image',
      minWidth: 342,
      minHeight: 496,
    },
    {
      name: 'filter',
      displayName: 'Filter attribute',
      type: 'attribute',
    },
  ],
});

export const AttributeSectionElement: FC<AttributeSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp, maxWidth, attributesCount } = props;
  const {
    connectors: { connect },
  } = useNode();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  return (
    <AttributeSection
      ref={(el) => el && connect(el)}
      {...props}
      title={
        <Element
          id="attribute_section_element_title"
          // @ts-expect-error why types are not compatible?
          is={AttributeTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_title'),
              'fr-FR': t('text_element_title'),
              'he-IL': t('text_element_title'),
            },
            titleProp,
          )}
          align="center"
          variant="h3"
          component="h3"
        />
      }
      subtitle={
        <Element
          id="attribute_section_element_subtitle"
          // @ts-expect-error why types are not compatible?
          is={AttributeTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_subtitle'),
              'fr-FR': t('text_element_subtitle'),
              'he-IL': t('text_element_subtitle'),
            },
            subtitleProp,
          )}
          align="center"
          variant="subtitle1"
        />
      }
      maxWidth={maxWidth}
    >
      <SectionSwiper slidesPerView={isMobile ? 1 : 4} centerInsufficientSlides={true}>
        {Array(Number(attributesCount))
          .fill(null)
          .map((_, index) => {
            // @ts-expect-error why types are not compatible?
            return <Element key={index} id={`attribute_${index + 1}`} is={AttributeElement} />;
          })}
      </SectionSwiper>
    </AttributeSection>
  );
};

const attributesChoices = getChoicesFromArray(
  Array(12)
    .fill(null)
    .map((_, i) => String(i + 1)),
);

Builder.registerComponent<ComponentSpec>(AttributeSectionElement, {
  name: 'AttributeSectionElement',
  displayName: 'Attribute section',
  inputs: [
    {
      name: 'attributesCount',
      displayName: 'Attributes count',
      type: 'select',
      choices: attributesChoices,
      defaultValue: '4',
    },
    {
      name: 'maxWidth',
      displayName: 'Max width',
      type: 'select',
      choices: getChoicesFromArray(['sm', 'md', 'lg', 'xl']),
      defaultValue: 'lg',
    },
  ],
});
