import { FC } from 'react';

import { useBuilderState } from '@king-david-it/builder';
import { Box } from '@mui/material';
import { NextImage } from '@packages/components';
import { BASE_PRICE_SLUG, MAX_RANGABLE_PREFIX, MIN_RANGABLE_PREFIX } from 'application/constants';
import { ButtonElementProps } from 'builder/elements/ButtonElement';
import NextLink from 'components/links/NextLink';
import { get } from 'lodash';

export interface SlotProps {
  actionButton: ButtonElementProps;
}

export type Slots = {
  [Key in keyof SlotProps]: FC<NonNullable<SlotProps[Key]>>;
};

export interface AttributeSectionViewProps {
  altAttributeName: string;
  src?: string;
  filter?: { slug: string; value: string | string[] };
  slots: Slots;
  slotProps?: SlotProps;
}

export const AttributeSectionView: FC<AttributeSectionViewProps> = (props) => {
  const { src, altAttributeName, filter, slots, slotProps } = props;
  const filterSlug = get(filter, 'slug') || '';
  const filterValue = get(filter, 'value') || get(filter, filterSlug);

  const { actionButton: ActionButton } = slots;
  const { actionButton: actionButtonProps } = slotProps || {};

  // TODO: replace with a commented code below in 1.5.0
  const getFormattedFilter = () => {
    if (filterSlug === BASE_PRICE_SLUG) {
      const [min, max] = Array.isArray(filterValue) ? filterValue : [];

      if (typeof min === 'undefined' || typeof max === 'undefined') {
        return {};
      }

      return {
        [`${MIN_RANGABLE_PREFIX}${BASE_PRICE_SLUG}`]: min,
        [`${MAX_RANGABLE_PREFIX}${BASE_PRICE_SLUG}`]: max,
      };
    }

    return {
      [filterSlug]: filterValue,
    };
  };

  const formattedFilter = getFormattedFilter();

  // const formattedFilter = formatFilterValues({
  //   values: {
  //     [filterSlug]: filterValue,
  //   },
  //   filterAttributes,
  //   independentFiltersSlugs: [],
  // });

  // TODOs end

  const { isEditing } = useBuilderState();

  return (
    <Box
      {...(isEditing
        ? { component: Box }
        : {
            component: NextLink,
            href: { pathname: 's', query: formattedFilter },
          })}
      height={496}
      width={1}
      position="relative"
      borderRadius={6}
      overflow="hidden"
      sx={{
        display: 'block',
        ...(!src
          ? {
              background: `linear-gradient(0deg, rgba(48, 48, 48, 0.68), rgba(48, 48, 48, 0.68))`,
            }
          : {}),
      }}
    >
      {src && (
        <NextImage
          src={src}
          fill
          style={{ objectFit: 'cover' }}
          alt={altAttributeName}
          // load smaller image version when on bigger screens
          sizes="(max-width: 768px) 100vw, (max-width: 1200px) 80vw, 70vw"
        />
      )}
      {<ActionButton {...actionButtonProps} />}
    </Box>
  );
};

export default AttributeSectionView;
