import { FC, useEffect, useMemo, useState } from 'react';

import { Builder, BuilderStateProvider } from '@king-david-it/builder';
import { SxProps } from '@mui/material';
import { createQueryClient, DeviceDetectContextProvider } from '@packages/providers';
import { Hydrate, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import { EMBEDDED_ROUTE, listingRoute } from 'application/constants';
import { WhatsAppFloatButton } from 'buttons/WhatsAppFloatButton';
import { CookiesContextProvider } from 'contexts/CookiesContext';
import { CurrencyContextProvider } from 'contexts/CurrencyContext';
import { LocaleContextProvider } from 'contexts/LocaleContext';
import { PageLoader } from 'custom/PageLoader';
import { SSRPageProps } from 'helpers/withServerSideProps';
import { useTranslatedZodErrorMap } from 'hooks/index';
import { AppProps } from 'next/app';
import Head from 'next/head';
import { appWithTranslation } from 'next-i18next';
import { CaptchaProvider } from 'providers/CaptchaProvider';
import { EmotionCacheProvider } from 'providers/EmotionCacheProvider';
import { ErrorHandlingManager } from 'providers/ErrorHandlingManager';
import { InterceptorsManager } from 'providers/InterseptorsManager';
import { SnackbarProvider } from 'providers/SnackbarProvider';
import { TagManagerProvider } from 'providers/TagManagerProvider';
import { ThemeProvider } from 'providers/ThemeProvider';
import { setErrorMap } from 'zod';

import nextI18NextConfig from '../next-i18next.config';

import 'theme/globalStyles.css';
import 'theme/icomoon.css';
import 'builder/menus/BaseMenu';

/**
 * There is no guarantee that for each page we get
 * all `SSRPageProps`, e.g. for 500 and 404 pages
 * we can't use `getServerSideProps`, so we make
 * `pageProps` partial. For `CustomApp` we can only
 * use `getInitialProps` which is tricky since it
 * runs both on client and server. Consider migrating
 * to server components to deal with this issue
 */
type CustomAppProps = AppProps<Partial<SSRPageProps>>;

const CustomApp: FC<CustomAppProps> = (props) => {
  const { Component, pageProps, router } = props;

  const isListingPage = router.pathname.includes(listingRoute.route);
  const isEmbeddedPage = router.pathname.includes(EMBEDDED_ROUTE);

  const [builderState] = useState(() => Builder.getStateFromURL(router.asPath));

  // This ensures that data is not shared between different users and requests
  const [queryClient] = useState(() => createQueryClient({ builderState }));

  const translatedZodErrorMap = useTranslatedZodErrorMap();

  useEffect(() => {
    setErrorMap(translatedZodErrorMap);
  }, [translatedZodErrorMap]);

  const getWhatsAppFloatButtonSx = useMemo(() => {
    return {
      ...(isListingPage ? { bottom: { xs: '5rem', sm: '1.5rem' } } : {}),
      ...(isEmbeddedPage ? { display: 'none' } : {}),
    } as SxProps;
  }, [isEmbeddedPage, isListingPage]);

  return (
    <CookiesContextProvider initialCookies={pageProps.cookies}>
      <DeviceDetectContextProvider userAgent={pageProps.userAgent}>
        <QueryClientProvider client={queryClient}>
          <Hydrate state={pageProps.dehydratedState}>
            <Head>
              <meta
                name="viewport"
                content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no"
              />
            </Head>
            <CaptchaProvider>
              <BuilderStateProvider value={builderState}>
                <LocaleContextProvider initialLocale={pageProps.locale}>
                  <CurrencyContextProvider initialCurrencyCode={pageProps.currencyCode}>
                    <InterceptorsManager />
                    <TagManagerProvider />
                    <EmotionCacheProvider>
                      <ThemeProvider>
                        <SnackbarProvider>
                          <ErrorHandlingManager />
                          <PageLoader>
                            <Component {...pageProps} />
                          </PageLoader>
                          {!builderState.isEditing && (
                            <WhatsAppFloatButton sx={getWhatsAppFloatButtonSx} />
                          )}
                        </SnackbarProvider>
                      </ThemeProvider>
                    </EmotionCacheProvider>
                  </CurrencyContextProvider>
                </LocaleContextProvider>
              </BuilderStateProvider>
            </CaptchaProvider>
            <ReactQueryDevtools initialIsOpen={false} />
          </Hydrate>
        </QueryClientProvider>
      </DeviceDetectContextProvider>
    </CookiesContextProvider>
  );
};

export default appWithTranslation(CustomApp, nextI18NextConfig);
