import { FC, ReactNode, useEffect, useState } from 'react';

import { CacheProvider } from '@emotion/react';
import { useLocaleContext } from 'contexts/LocaleContext';
import { createEmotionCache } from 'theme/helpers/createEmotionCache';

interface EmotionCacheProviderProps {
  children: ReactNode;
}

export const EmotionCacheProvider: FC<EmotionCacheProviderProps> = (props) => {
  const { children } = props;

  const { locale } = useLocaleContext();

  const [emotionCache, setEmotionCache] = useState(() => createEmotionCache(locale));

  useEffect(() => {
    // Update emotion cache to correctly switch between rtl and ltr
    setEmotionCache(createEmotionCache(locale));
  }, [locale]);

  return <CacheProvider value={emotionCache}>{children}</CacheProvider>;
};
