import createCache from '@emotion/cache';
import { isBrowser } from '@packages/helpers';
import { getDirectionFromLocale } from 'helpers/getDirectionFromLocale';
import { prefixer } from 'stylis';
import rtlPlugin from 'stylis-plugin-rtl';

const ltrCache = createCache({
  key: 'mui',
  prepend: false,
});

const rtlCache = createCache({
  key: 'mui-rtl',
  prepend: false,
  stylisPlugins: [prefixer, rtlPlugin],
});

export const createEmotionCache = (locale?: string) => {
  const direction = getDirectionFromLocale(locale);

  const isRtl = direction === 'rtl';

  if (isBrowser) {
    document.documentElement.dir = direction;

    return isRtl ? rtlCache : ltrCache;
  }

  // Emotion cache must be recreated on each request on server side
  // @see https://mui.com/material-ui/guides/server-rendering/
  return createCache({
    key: isRtl ? 'mui-rtl' : 'mui',
    ...(isRtl && { stylisPlugins: [prefixer, rtlPlugin] }),
    // Append styles to the bottom of head tag to override rtl or ltr previous styles
    prepend: false,
  });
};
