import { createContext, FC, ReactNode, useContext } from 'react';

import { noop } from 'lodash';

import { UseListingButtonsControllerReturn } from './useListingButtonsController';

interface ListingButtonsContextProviderProps {
  children: ReactNode;
  value: UseListingButtonsControllerReturn;
}

const defaultContextValue = {
  activeIndex: 0,
  setActiveIndex: noop,
  listings: {},
  setListings: noop,
};

export const ListingButtonsContext = createContext(defaultContextValue);

export const useListingButtonsContext = () => {
  const context = useContext(ListingButtonsContext);

  return context;
};

export const ListingButtonsContextProvider: FC<ListingButtonsContextProviderProps> = (props) => {
  const { children, value } = props;

  return <ListingButtonsContext.Provider value={value}>{children}</ListingButtonsContext.Provider>;
};
