import { FC } from 'react';

import { Builder, Element, useFilesContext, useNode } from '@king-david-it/builder';
import { SearchSection } from 'components/pageTemplates/HomepageTemplate';
import { useTranslate } from 'hooks/index';
import { merge } from 'lodash';

import { ButtonElement, ButtonElementProps } from '../ButtonElement';
import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

export interface SearchSectionElementProps {
  title: Record<string, string>;
  subtitle: Record<string, string>;
  desktopImageId?: string;
  mobileImageId?: string;
}

const SearchSectionTitleElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement
    sx={{
      wordBreak: 'break-word',
      mb: 1,
      mx: { xs: 5, md: 0 },
      ...sx,
    }}
    {...rest}
  />
);

Builder.registerComponent<ComponentSpec>(SearchSectionTitleElement, {
  ...textElementSettings,
  name: 'SearchSectionTitleElement',
});

const SearchSectionSubitleElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ mb: 6, mx: { xs: 5, md: 0 }, ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(SearchSectionSubitleElement, {
  ...textElementSettings,
  name: 'SearchSectionSubitleElement',
});

const SearchButtonElement: FC<ButtonElementProps> = (props) => {
  return <Element id="search_button" is={ButtonElement} {...props} />;
};

export const SearchSectionElement: FC<SearchSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp, desktopImageId, mobileImageId } = props;

  const {
    connectors: { connect },
  } = useNode();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });
  const { getFile } = useFilesContext();

  return (
    <SearchSection
      ref={(el: HTMLElement) => el && connect(el)}
      title={
        <Element
          id="search_section_element_title"
          // @ts-expect-error why types are not compatible?
          is={SearchSectionTitleElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_title'),
              'fr-FR': t('text_element_title'),
              'he-IL': t('text_element_title'),
            },
            titleProp,
          )}
          align="center"
          variant="h1"
          component="h1"
          color="primary.contrastText"
        />
      }
      subtitle={
        <Element
          id="search_section_element_subtitle"
          // @ts-expect-error why types are not compatible?
          is={SearchSectionSubitleElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_subtitle'),
              'fr-FR': t('text_element_subtitle'),
              'he-IL': t('text_element_subtitle'),
            },
            subtitleProp,
          )}
          align="center"
          variant="subtitle2"
          component="h3"
          color="primary.contrastText"
        />
      }
      slots={{
        actionButton: SearchButtonElement,
      }}
      slotProps={{
        actionButton: {
          text: {
            'en-US': 'Search',
            'fr-FR': 'Rechercher',
            'he-IL': '',
          },
        },
      }}
      desktopImageSrc={getFile(desktopImageId)?.src}
      mobileImageSrc={getFile(mobileImageId)?.src}
    />
  );
};

Builder.registerComponent<ComponentSpec>(SearchSectionElement, {
  name: 'SearchSectionElement',
  displayName: 'Search section',
  inputs: [
    {
      name: 'desktopImageId',
      displayName: 'Desktop image',
      type: 'image',
      minWidth: 1920,
      minHeight: 1000,
    },
    {
      name: 'mobileImageId',
      displayName: 'Mobile image',
      type: 'image',
      minWidth: 420,
      minHeight: 800,
    },
  ],
});
