import { FC, lazy, ReactNode, Suspense } from 'react';

import { PhoneInputProps } from './PhoneInput';

export interface PhoneInputLazyProps extends PhoneInputProps {
  fallback?: ReactNode;
}

const PhoneInput = lazy(() => import('./PhoneInput'));

export const PhoneInputLazy: FC<PhoneInputLazyProps> = (props) => {
  const { fallback = null, ...rest } = props;
  return (
    <Suspense fallback={fallback}>
      <PhoneInput {...rest} />
    </Suspense>
  );
};

export default PhoneInputLazy;
