import { FC } from 'react';

import { Element, useNode } from '@king-david-it/builder';
import { Breakpoint, Container, Grid } from '@mui/material';
import { Builder } from 'components/custom/Builder';
import { ContactForm, ContactFormProps } from 'components/forms/ContactForm';
import { GridItem } from 'templates/GridItem';
import { ContactsInfoView, ContactsInfoViewProps } from 'views/ContactsInfoView';

import { ButtonElement, ButtonElementProps } from './ButtonElement';
import { getChoicesFromArray } from './helpers/getChoicesFromArray';
import { ComponentSpec } from './types';

export interface ContactFormElementProps extends ContactFormProps, ContactsInfoViewProps {
  maxWidth?: Breakpoint;
}

export const ContactFormElement: FC<ContactFormElementProps> = (props) => {
  const { maxWidth = 'lg', email, location, phone, mediaLinks } = props;
  const {
    connectors: { connect },
  } = useNode();

  return (
    <Container maxWidth={maxWidth} ref={(el) => el && connect(el)}>
      <Grid container spacing={3}>
        <GridItem md={9}>
          <ContactForm
            BuilderButton={(props: ButtonElementProps) => (
              <Element id="contact_form_submit_button" is={ButtonElement} {...props} />
            )}
          />
        </GridItem>
        <GridItem md={3}>
          <ContactsInfoView
            email={email}
            location={location}
            phone={phone}
            mediaLinks={mediaLinks}
          />
        </GridItem>
      </Grid>
    </Container>
  );
};

Builder.registerComponent<ComponentSpec>(ContactFormElement, {
  rootProps: {
    py: 4,
  },
  name: 'ContactFormElement',
  displayName: 'Contact form',
  inputs: [
    {
      name: 'maxWidth',
      displayName: 'Max width',
      type: 'select',
      choices: getChoicesFromArray(['sm', 'md', 'lg', 'xl']),
      defaultValue: 'lg',
    },
    {
      name: 'email',
      displayName: 'Email',
      type: 'string',
      defaultValue: 'Enter some email here...',
    },
    {
      name: 'phone',
      displayName: 'Phone',
      type: 'string',
      defaultValue: 'Enter some phone here...',
    },
    {
      name: 'location',
      displayName: 'Location',
      type: 'string',
      defaultValue: 'Enter some location here...',
      isTranslatable: true,
    },
    {
      name: 'mediaLinks.facebookLink',
      displayName: 'Facebook link',
      type: 'string',
    },
    {
      name: 'mediaLinks.instagramLink',
      displayName: 'Instagram link',
      type: 'string',
    },
    {
      name: 'mediaLinks.twitterLink',
      displayName: 'Twitter link',
      type: 'string',
    },
  ],
});
