import { useBuilderState } from '@king-david-it/builder';
import { useGetWebsite } from 'hooks/index';
import get from 'lodash/get';
import Script from 'next/script';

export const TagManagerProvider = () => {
  const { data: websiteData } = useGetWebsite();

  const gtmCode = get(websiteData, 'gtm_code');

  const { isEditing } = useBuilderState();

  if (isEditing || !gtmCode) {
    return null;
  }

  /* <!-- Google Tag Manager --> */
  return (
    <Script id="google-tag-manager">
      {`(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
        new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
        j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
        'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
        })(window,document,'script','dataLayer', '${gtmCode}');`}
    </Script>
  );
};
