import { FC } from 'react';

import { Builder, Element, useNode } from '@king-david-it/builder';
import { DEFAULT_LOCALE } from 'application/constants';
import {
  ContactUsSection,
  ContactUsSectionProps,
} from 'components/pageTemplates/HomepageTemplate/components/ContactUsSection';
import { useLocaleContext } from 'contexts/LocaleContext';
import { useTranslate } from 'hooks/index';
import { get, merge } from 'lodash';

import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

export interface ContactUsSectionElementProps extends Omit<ContactUsSectionProps, 'text'> {
  text?: Record<string, string>;
}

const ContactUsTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ wordBreak: 'break-word', ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(ContactUsTextElement, {
  ...textElementSettings,
  name: 'ContactUsTextElement',
});

export const ContactUsSectionElement: FC<ContactUsSectionElementProps> = (props) => {
  const { text: textProp, address: addressProp } = props;
  const {
    connectors: { connect },
  } = useNode();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });

  const { locale } = useLocaleContext();

  const address = get(addressProp, locale) || get(addressProp, DEFAULT_LOCALE);

  return (
    <ContactUsSection
      ref={(el) => el && connect(el)}
      {...props}
      text={
        <Element
          id="contact_us_section_element_text"
          // @ts-expect-error why types are not compatible?
          is={ContactUsTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element'),
              'fr-FR': t('text_element'),
              'he-IL': t('text_element'),
            },
            textProp,
          )}
          align="center"
          variant="subtitle1"
        />
      }
      address={address}
    />
  );
};

Builder.registerComponent<ComponentSpec>(ContactUsSectionElement, {
  name: 'ContactUsSectionElement',
  displayName: 'Contact us section',
  inputs: [
    {
      name: 'email',
      displayName: 'Email',
      type: 'string',
      defaultValue: 'Enter some email here...',
    },
    {
      name: 'phone',
      displayName: 'Phone',
      type: 'string',
      defaultValue: 'Enter some phone here...',
    },
    {
      name: 'address',
      displayName: 'Address',
      type: 'string',
      defaultValue: 'Enter some address here...',
      isTranslatable: true,
    },
    {
      name: 'mediaLinks.facebookLink',
      displayName: 'Facebook link',
      type: 'string',
    },
    {
      name: 'mediaLinks.instagramLink',
      displayName: 'Instagram link',
      type: 'string',
    },
    {
      name: 'mediaLinks.twitterLink',
      displayName: 'Twitter link',
      type: 'string',
    },
  ],
});
