import { FC } from 'react';

import { Builder, Element, useNode } from '@king-david-it/builder';
import { Divider, Grid, Theme, useMediaQuery } from '@mui/material';
import { listingRoute } from 'application/constants';
import {
  HomeReviewsSection,
  HomeReviewsSectionProps,
} from 'components/sections/HomeReviewsSection';
import { useTranslate } from 'hooks/index';
import useGetRewiews from 'hooks/queries/useGetRewiews';
import { merge } from 'lodash';
import { GridItem } from 'templates/GridItem';
import ReviewItemView, { ReviewItemViewProps } from 'views/ReviewItemView/ReviewItemView';

import { getChoicesFromArray } from '../helpers/getChoicesFromArray';
import { TextElement, TextElementProps, textElementSettings } from '../TextElement';
import { ComponentSpec } from '../types';

export interface ReviewsSectionElementProps
  extends Omit<HomeReviewsSectionProps, 'title' | 'subtitle'> {
  reviewsCount: string;
  title?: Record<string, string>;
  subtitle?: Record<string, string>;
}

const ReviewsTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement sx={{ wordBreak: 'break-word', ...sx }} {...rest} />
);

Builder.registerComponent<ComponentSpec>(ReviewsTextElement, {
  ...textElementSettings,
  name: 'ReviewsTextElement',
});

export const ReviewItemElement: FC<ReviewItemViewProps> = (props) => {
  return null;
};

Builder.registerComponent<ComponentSpec>(ReviewItemElement, {
  // TODO: to remove registration when builder is fixed and we don't see  "TypeError: Cannot read properties of undefined (reading 'name')" in panel
  name: 'ReviewItemElement',
  displayName: 'Review item',
});

export const ReviewsSectionElement: FC<ReviewsSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp, reviewsCount, maxWidth } = props;
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('sm'));
  const {
    connectors: { connect },
  } = useNode();
  const t = useTranslate({ basePath: 'builder.elements.default_values' });

  const reviewsResult = useGetRewiews({ perPage: Number(reviewsCount) });

  return (
    <HomeReviewsSection
      ref={(el) => el && connect(el)}
      {...props}
      title={
        <Element
          id="review_section_element_title"
          // @ts-expect-error why types are not compatible?
          is={ReviewsTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_title'),
              'fr-FR': t('text_element_title'),
              'he-IL': t('text_element_title'),
            },
            titleProp,
          )}
          align="center"
          variant="h3"
          component="h3"
        />
      }
      subtitle={
        <Element
          id="review_section_element_subtitle"
          // @ts-expect-error why types are not compatible?
          is={ReviewsTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_subtitle'),
              'fr-FR': t('text_element_subtitle'),
              'he-IL': t('text_element_subtitle'),
            },
            subtitleProp,
          )}
          align="center"
          variant="subtitle1"
        />
      }
      maxWidth={maxWidth}
    >
      <Grid container spacing={{ xs: 3, md: 5 }}>
        {reviewsResult.isSuccess &&
          reviewsResult.data.map((reviewData, index) => {
            const isShowDivider = isMobile
              ? index !== Number(reviewsCount) - 1
              : index < Number(reviewsCount) - 2;

            return (
              <GridItem
                key={index}
                sm={6}
                display="flex"
                justifyContent="space-between"
                flexDirection="column"
              >
                <ReviewItemView
                  firstName={reviewData.user?.first_name}
                  avatarSrc={reviewData.user?.avatar?.src || ''}
                  comment={reviewData.review_text}
                  date={reviewData.added_at}
                  rating={reviewData.rating}
                  place={reviewData.listing?.title}
                  listingHref={listingRoute.getPathname(reviewData.listing.slug)}
                />
                {isShowDivider && <Divider sx={{ mt: { xs: 2, md: 3 } }} />}
              </GridItem>
            );
          })}
      </Grid>
    </HomeReviewsSection>
  );
};

const reviewsChoices = getChoicesFromArray(
  Array(12)
    .fill(null)
    .map((_, i) => String(i + 1)),
);
Builder.registerComponent<ComponentSpec>(ReviewsSectionElement, {
  name: 'ReviewsSectionElement',
  displayName: 'Reviews section',
  inputs: [
    {
      name: 'reviewsCount',
      displayName: 'Reviews count',
      type: 'select',
      choices: reviewsChoices,
      defaultValue: '4',
    },
    {
      name: 'maxWidth',
      displayName: 'Max width',
      type: 'select',
      choices: getChoicesFromArray(['sm', 'md', 'lg', 'xl']),
      defaultValue: 'lg',
    },
  ],
});
