import { FC, useCallback, useEffect } from 'react';

import { QueryMeta } from '@packages/types';
import { Query, useQueryClient } from '@tanstack/react-query';
import { useErrorHandler } from 'hooks/useErrorHandler';

export const ErrorHandlingManager: FC = () => {
  const queryClient = useQueryClient();
  const { handleQueryErrors } = useErrorHandler();

  const onQueryError = useCallback(
    (error: unknown, query: Query) => {
      const meta: QueryMeta = query.options.meta || {};
      const { errorOptions } = meta;

      handleQueryErrors(error, errorOptions);
    },
    [handleQueryErrors],
  );

  useEffect(() => {
    const queryCache = queryClient.getQueryCache();
    const defaultOnError = queryCache.config.onError;
    queryCache.config.onError = (...args) => {
      defaultOnError?.(...args);
      onQueryError(...args);
    };
  }, [queryClient, onQueryError]);

  return null;
};
