import { Builder, useNode, UserComponent } from '@king-david-it/builder';
import { Box, useTheme } from '@mui/material';
import { DEFAULT_LOCALE } from 'application/constants';
import { useLocaleContext } from 'contexts/LocaleContext';
import { get, set } from 'lodash';
import dynamic from 'next/dynamic';

import 'quill/dist/quill.snow.css';
import { ComponentSpec } from './types';

const QuillNoSSRWrapper = dynamic(import('react-quill'), {
  ssr: false,
  loading: () => <p>Loading ...</p>,
});

const formats = [
  'header',
  'size',
  'bold',
  'italic',
  'underline',
  'strike',
  'blockquote',
  'list',
  'bullet',
  'indent',
  'link',
];

export interface RichTextElementProps {
  text: Record<string, string>;
}

export const RichTextElement: UserComponent<RichTextElementProps> = (props) => {
  const { text: textProp } = props;
  const { locale } = useLocaleContext();
  const theme = useTheme();
  const {
    actions: { setProp },
    connectors: { connect },
    isSelected,
  } = useNode((node) => ({ isSelected: node.events.selected }));

  const modules = {
    toolbar: isSelected
      ? [
          [{ header: [1, 2, 3, 4, 5, 6, false] }],
          ['bold', 'italic', 'underline', 'strike', 'blockquote'],
          [{ list: 'ordered' }, { list: 'bullet' }, { indent: '-1' }, { indent: '+1' }],
          ['link'],
          ['clean'],
        ]
      : false,
    clipboard: {
      // toggle to add extra line breaks when pasting HTML:
      matchVisual: false,
    },
  };

  const text = get(textProp, locale) || get(textProp, DEFAULT_LOCALE);

  const handleChange = (value: string) => {
    if (!isSelected) return;

    setProp((props: Record<string, unknown>) => {
      set(props, `text.${locale}`, value);
    }, 500);
  };

  return (
    <Box
      ref={(el: HTMLElement) => el && connect(el)}
      sx={{
        '.ql-snow': {
          '.ql-editor': {
            ...theme.typography,
            p: theme.typography.body1,
            textAlign: 'left',
            '.ql-size-small': theme.typography.body2,
          },
          '&.ql-container, &.ql-toolbar': {
            border: isSelected ? '1px solid' : 'none',
            borderColor: 'action.disabled',
          },
          '& .ql-picker.ql-expanded .ql-picker-options': {
            zIndex: 1501,
          },
          '& .ql-picker .ql-picker-label svg': {
            left: 'unset',
          },
        },
      }}
    >
      {isSelected ? (
        <QuillNoSSRWrapper
          key="selected"
          value={text}
          modules={modules}
          formats={formats}
          theme="snow"
          onChange={handleChange}
        />
      ) : (
        <QuillNoSSRWrapper
          key="not-selected"
          value={text}
          modules={modules}
          formats={formats}
          theme="snow"
          readOnly
        />
      )}
    </Box>
  );
};

RichTextElement.craft = {
  defaultProps: {
    text: {
      [DEFAULT_LOCALE]: 'Enter some text...',
    },
  },
};

Builder.registerComponent<ComponentSpec>(RichTextElement, {
  name: 'RichTextElement',
  displayName: 'Rich text',
  icon: '/icons/richText.svg',
});
