import { useCallback, useState } from 'react';

export interface UseListingButtonsControllerReturn {
  activeIndex: number;
  setActiveIndex: (index: number) => void;
  listings: Record<string, string[]>;
  setListings: (selectedListingIds: string[], listingNodeId: string) => void;
}

export const useListingButtonsController = (): UseListingButtonsControllerReturn => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [listings, setListings] = useState({});

  const onChangeListings = useCallback((ids: string[], id: string) => {
    setListings((prev) => ({ ...prev, [id]: ids }));
  }, []);

  return { activeIndex, setActiveIndex, listings, setListings: onChangeListings };
};
