import { FC } from 'react';

import { Builder, Element, useFilesContext, useNode } from '@king-david-it/builder';
import { PageHeadingSection } from 'components/custom/PageHeadingSection';
import { useTranslate } from 'hooks/index';
import { merge } from 'lodash';

import { TextElement, TextElementProps, textElementSettings } from './TextElement';
import { ComponentSpec } from './types';

export interface PageHeadingSectionElementProps {
  title?: Record<string, string>;
  subtitle?: Record<string, string>;
  desktopImageId?: string;
  mobileImageId?: string;
}

const PageHeadingTextElement: FC<TextElementProps> = ({ sx, ...rest }) => (
  <TextElement
    sx={{
      wordBreak: 'break-word',
      mx: { sm: 5, md: 0 },
      ...sx,
    }}
    {...rest}
  />
);

Builder.registerComponent<ComponentSpec>(PageHeadingTextElement, {
  ...textElementSettings,
  name: 'PageHeadingTextElement',
});

export const PageHeadingSectionElement: FC<PageHeadingSectionElementProps> = (props) => {
  const { title: titleProp, subtitle: subtitleProp, desktopImageId, mobileImageId } = props;

  const { getFile } = useFilesContext();

  const t = useTranslate({ basePath: 'builder.elements.default_values' });

  const {
    connectors: { connect },
  } = useNode();

  return (
    <PageHeadingSection
      ref={(el) => el && connect(el)}
      title={
        <Element
          id="page_heading_element_title"
          // @ts-expect-error why types are not compatible?
          is={PageHeadingTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_title'),
              'fr-FR': t('text_element_title'),
              'he-IL': t('text_element_title'),
            },
            titleProp,
          )}
          color="primary.contrastText"
          align="center"
          variant="h1"
          component="h1"
        />
      }
      subtitle={
        <Element
          id="page_heading_element_subtitle"
          // @ts-expect-error why types are not compatible?
          is={PageHeadingTextElement}
          // [PREV_VALUES_SUPPORT] - see COMMENTS.md
          text={merge(
            {
              'en-US': t('text_element_subtitle'),
              'fr-FR': t('text_element_subtitle'),
              'he-IL': t('text_element_subtitle'),
            },
            subtitleProp,
          )}
          color="primary.contrastText"
          align="center"
          variant="h3"
          component="h3"
        />
      }
      desktopImageSrc={getFile(desktopImageId)?.src}
      mobileImageSrc={getFile(mobileImageId)?.src}
    />
  );
};

Builder.registerComponent<ComponentSpec>(PageHeadingSectionElement, {
  name: 'PageHeadingSectionElement',
  displayName: 'Page heading',
  inputs: [
    {
      name: 'desktopImageId',
      displayName: 'Desktop image',
      type: 'image',
      minWidth: 1920,
      minHeight: 496,
    },
    {
      name: 'mobileImageId',
      displayName: 'Mobile image',
      type: 'image',
      minWidth: 420,
      minHeight: 800,
    },
  ],
});
