import { ReactNode } from 'react';

import { Box, BoxProps } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { Avatar } from 'custom/Avatar';
import { ImageProps } from 'next/image';
import { SocialsToolbar, SocialsToolbarProps } from 'views/SocialsToolbar';

import { Typography } from '../../Typography';

export interface VerticalAvatarViewProps
  extends Pick<SocialsToolbarProps, 'facebookLink' | 'instagramLink' | 'twitterLink'>,
    StyledRootProps {
  title: ReactNode;
  subtitle: ReactNode;
  avatarSrc?: ImageProps['src'];
  avatarPropsSx?: BoxProps['sx'];
  socialsToolbarProps?: Partial<SocialsToolbarProps>;
  size?: number;
}

export function VerticalAvatarView(props: VerticalAvatarViewProps) {
  const {
    title,
    subtitle,
    avatarSrc,
    avatarPropsSx = {},
    sx,
    facebookLink,
    instagramLink,
    twitterLink,
    socialsToolbarProps,
    className,
    size = 80,
  } = props;

  return (
    <Box
      sx={{
        maxWidth: 220,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        ...sx,
      }}
      className={className}
    >
      <Avatar
        src={avatarSrc}
        size={size}
        sx={{
          ...avatarPropsSx,
        }}
      />

      <Typography variant="h5" sx={{ mt: 3, textAlign: 'center' }}>
        {title}
      </Typography>
      {!!subtitle && <Typography variant="subtitle1">{subtitle}</Typography>}
      <SocialsToolbar
        facebookLink={facebookLink}
        instagramLink={instagramLink}
        twitterLink={twitterLink}
        sx={{ mt: 3 }}
        {...socialsToolbarProps}
      />
    </Box>
  );
}

export default VerticalAvatarView;
