import { FC, forwardRef, ReactNode } from 'react';

import { Box, BoxProps } from '@mui/material';
import { NextImage } from '@packages/components';
import { StyledRootProps, WithRootProps } from '@packages/types';
import { DarkenedImageBackground } from 'custom/DarkenedImageBackground';

export type ImageTemplateProps = WithRootProps<
  StyledRootProps,
  {
    title: ReactNode;
    subtitle: ReactNode;
    mobileImageSrc?: string;
    desktopImageSrc?: string;
    ref?: BoxProps['ref'];
    children?: ReactNode;
  }
>;

export const ImageTemplate: FC<ImageTemplateProps> = forwardRef((props, ref) => {
  const { sx, className, mobileImageSrc, desktopImageSrc, children, title, subtitle } = props;

  return (
    <Box
      ref={ref}
      className={className}
      sx={{
        minHeight: 496,
        backgroundSize: 'cover',
        position: 'relative',
        ...sx,
      }}
    >
      <DarkenedImageBackground />
      {mobileImageSrc ? (
        <Box sx={{ display: { md: 'none' } }}>
          <NextImage
            src={mobileImageSrc}
            alt="Page heading"
            fill
            style={{ objectFit: 'cover' }}
            priority
          />
        </Box>
      ) : null}
      {desktopImageSrc ? (
        <Box sx={{ display: { xs: 'none', md: 'block' } }}>
          <NextImage
            src={desktopImageSrc}
            alt="Page heading"
            fill
            style={{ objectFit: 'cover' }}
            priority
          />
        </Box>
      ) : null}

      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', px: 3, py: 5 }}>
        <Box
          sx={{
            width: { xs: '100%', md: 'auto' },
            zIndex: 2,
          }}
        >
          {title}
          {subtitle}

          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            {children}
          </Box>
        </Box>
      </Box>
    </Box>
  );
});

ImageTemplate.displayName = 'ImageTemplate';
